import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import './FooterNav.css';
//import PcPrivacyPolicy from "../../pages/PC/PCFooterNav/PrivacyPolicy/PcPrivacyPolicy";
import APIHelper from '../../utils/APIHelper';

function FooterNav() {

    const [data, setData] = useState("©︎ ONeW株式会社　公式通販サイト");
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);


    useEffect(() => {
        async function fetchData() {
            try {
                const response = await APIHelper.getShopCopyright();
                setData(response.data.data); // 确保 response.data 是你想要的数据结构
                setLoading(false);
            } catch (error) {
                console.error("Error fetching data:", error);
                setError(error);
                setLoading(false);
            }
        }

        fetchData();
    }, []);



    return (
        <div className="footer-nav">


            <div className="nav-links">
                <span className="nav-link-vertical-line"></span>
                <Link to="/PcAQ" className="nav-link">お問い合わせ</Link>
                <span className="nav-link-vertical-line"></span>
                <Link to="/PcPrivacyPolicy" className="nav-link">プライバシーポリシー</Link>
                <span className="nav-link-vertical-line"></span>
                <Link to="/PcDescription" className="nav-link">特定商取引法に基づく表記</Link>
                <span className="nav-link-vertical-line"></span>
                <Link to="/PcCompany" className="nav-link">会社概要</Link>
                <span className="nav-link-vertical-line"></span>
            </div>


            <div className="logos">
                <div className="nav-logo">
                    <img src={process.env.PUBLIC_URL + '/assets/img/PC/PcHome/logo2.jpg'} alt="First Logo" className="logo-img" />
                </div>
                <div className="nav-logo-right">
                    {loading ? <span>Loading...</span> : error ? <span>Error loading data</span> : <span>{data}</span>}
                </div>
            </div>
        </div>
    );
}

export default FooterNav;