import React, { useEffect, useState } from 'react';
import { Link, useNavigate, useParams } from 'react-router-dom';
import axios from 'axios';
import './ProductDetail.css';
import PciTemCarousel from "./PciTemCarousel";
import filterMoney from '../../../utils/filterMoney';
import APIHelper from '../../../utils/APIHelper';
//import Heart from "../../../components/Heart";

const ProductDetails = () => {
    const url = window.location.href;
    const parts = url.split('/');
    const productId = parts[parts.length - 1];
    const [product, setProduct] = useState({});
    const quantity = 1;
    const [responseMessage, setResponseMessage] = useState('');
    const [productImg, setProductImg] = useState([]);
    const { image1, name, afterTaxPrice, inventoryQuantity: stock, description, id } = product; // 解构
    const [viewImg, setViewImg] = useState('')
    //const [mainImage, setMainImage] = useState(""); //传递子组件值，轮播图
    const [imagesArray, setImagesArray] = useState([]);
    const navigate = useNavigate()


    useEffect(() => {
        const fetchProduct = async () => {
            try {
                const response = await APIHelper.getProductDetail(productId);
                const data = response.data;
                if (data.code) {
                    console.log(data);
                    setProduct(data.data.product);

                    // let tempImages = [];
                    // for (let i = 2; i <= 10; i++) {
                    //     const imageUrl = data.data.product[`image${i}`];
                    //         console.log(imageUrl);
                    //         tempImages.push(imageUrl);
                    // }
                    // setImagesArray(tempImages);
                }

            } catch (err) {
                console.log(err);
            }
        };
        fetchProduct();
    }, [productId]);


    const addToCart = () => {
        const token = sessionStorage.getItem('reborn-ec-token'); // 从sessionStorage中取得token

        if (!token) {
            alert("ログインしてください");
            return; // 如果没有 token, 直接返回
        }

        APIHelper.addShop(productId, quantity).then(response => {
            console.log(response);
            alert("カートに追加されました");
            setResponseMessage(response.data.message || 'Successfully added to cart!');
        })
            .catch(error => {
                alert("カートに追加できませんでした");
                setResponseMessage(error.message || 'Failed to add to cart.');
            });
    };

    const addFavorites = () => {
        APIHelper.handleFavorite(productId, false).then(() => alert("商品をお気に入りに追加しました"));
    }
    console.log(responseMessage);


    return (
        <>
            <div className="product-detail-container">
                <div className="product-detail">
                    <div className="left-section" >

                        <img src={viewImg || image1} alt="product" className="main-image" />
                        <div className="">
                            <PciTemCarousel setImg={(img) => setViewImg(img)} />
                        </div>
                    </div>
                    <div className="right-section">
                        <p style={{ width: "460px", height: "60px", wordWrap: 'break-word', fontSize: "30px" }}>{name}</p>
                        <p className="price">{filterMoney(afterTaxPrice)}</p>
                        <p className="stock">在庫数: {stock}</p>
                        <p className="description">{description}</p>
                        <div onClick={addToCart} className="product-detail-cartBtn">

                            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M3 2.25C2.58579 2.25 2.25 2.58579 2.25 3C2.25 3.41421 2.58579 3.75 3 3.75L3 2.25ZM5.5 3L6.22761 2.8181C6.14414 2.48422 5.84415 2.25 5.5 2.25V3ZM19 16.75C19.4142 16.75 19.75 16.4142 19.75 16C19.75 15.5858 19.4142 15.25 19 15.25V16.75ZM21 5L21.7276 5.1819C21.7836 4.95785 21.7333 4.72048 21.5912 4.53844C21.449 4.3564 21.231 4.25 21 4.25V5ZM19 13V13.75C19.3442 13.75 19.6441 13.5158 19.7276 13.1819L19 13ZM3 3.75H5.5V2.25H3L3 3.75ZM4.77239 3.1819L5.27239 5.1819L6.72761 4.8181L6.22761 2.8181L4.77239 3.1819ZM5.27239 5.1819L7.27239 13.1819L8.72761 12.8181L6.72761 4.8181L5.27239 5.1819ZM8 12.25H7.5V13.75H8V12.25ZM7.5 16.75H19V15.25H7.5V16.75ZM5.25 14.5C5.25 15.7426 6.25736 16.75 7.5 16.75V15.25C7.08579 15.25 6.75 14.9142 6.75 14.5H5.25ZM7.5 12.25C6.25736 12.25 5.25 13.2574 5.25 14.5H6.75C6.75 14.0858 7.08579 13.75 7.5 13.75V12.25ZM6 5.75H21V4.25H6V5.75ZM20.2724 4.8181L18.2724 12.8181L19.7276 13.1819L21.7276 5.1819L20.2724 4.8181ZM19 12.25H8V13.75H19V12.25ZM18.25 20C18.25 20.1381 18.1381 20.25 18 20.25V21.75C18.9665 21.75 19.75 20.9665 19.75 20H18.25ZM18 20.25C17.8619 20.25 17.75 20.1381 17.75 20H16.25C16.25 20.9665 17.0335 21.75 18 21.75V20.25ZM17.75 20C17.75 19.8619 17.8619 19.75 18 19.75V18.25C17.0335 18.25 16.25 19.0335 16.25 20H17.75ZM18 19.75C18.1381 19.75 18.25 19.8619 18.25 20H19.75C19.75 19.0335 18.9665 18.25 18 18.25V19.75ZM8.25 20C8.25 20.1381 8.13807 20.25 8 20.25V21.75C8.9665 21.75 9.75 20.9665 9.75 20H8.25ZM8 20.25C7.86193 20.25 7.75 20.1381 7.75 20H6.25C6.25 20.9665 7.0335 21.75 8 21.75V20.25ZM7.75 20C7.75 19.8619 7.86193 19.75 8 19.75V18.25C7.0335 18.25 6.25 19.0335 6.25 20H7.75ZM8 19.75C8.13807 19.75 8.25 19.8619 8.25 20H9.75C9.75 19.0335 8.9665 18.25 8 18.25V19.75Z" fill="#F4F4F4" />
                            </svg>
                            <span>カートに入れる</span>
                        </div>


                        <div className="product-detail-LoveBtn" onClick={addFavorites}>
                            <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                                <path d="M21 8.50003C21 14.7444 12.0004 20 12.0004 20C12.0004 20 3 14.6667 3 8.51268C3 6.00003 5 4.00003 7.5 4.00003C10 4.00003 12 7.00003 12 7.00003C12 7.00003 14 4.00003 16.5 4.00003C19 4.00003 21 6.00003 21 8.50003Z" stroke="#F4F4F4" />
                            </svg>
                            <span>お気に入り登録</span>
                        </div>

                    </div>
                </div>
            </div>
            <div style={{ width: '100%', height: "200px", backgroundColor: "#fff" }}></div>
        </>
    );
}

export default ProductDetails;





