import React from "react";
import './PCAccountManagement.css'
import PcMyPageNav from "../PcMyPageNav/PcMyPageNav";



function PCAccountManagement(){

    return(
        <>
            <div className="MyPageNav">
                <div className="MyPageText">HOME / MY PAGE / アカウント管理</div>
            </div>
            <div className="All">
                MY PAGE
            </div>
            <div className="PCAccountManagement-container">
                <div className="PCAccountManagement-container-center">
                   <div className="PCAccountManagement-container-center-left">
                       <PcMyPageNav/>
                   </div>
                    <div className="PCAccountManagement-container-center-right">
                        <div className="PCAccountManagement-container-center-right-text">
                             アカウント管理
                        </div>

                        <div className="PCAccountManagement-container-center-right-show">
                            <div className="PCAccountManagement-container-center-right-show-inside">
                                <input type="text"/>
                                <button style={{
                                    backgroundColor:"#F5CE69"
                                }}>パスワードリセット</button>
                                <button style={{
                                    backgroundColor:"#F57948"
                                }}>ログアウト</button>
                            </div>

                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}

export default PCAccountManagement;