import React, { useEffect, useState } from "react";
import './PcPrivacyPolicy.css'
import axios from "axios";
import APIHelper from "../../../../utils/APIHelper";


function PcPrivacyPolicy() {


    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {

        APIHelper.getPrivacy()
            .then((response) => {
                setData(response.data.data);
                setLoading(false);
            })
            .catch((err) => {
                setError(err);
                setLoading(false);
            });
    }, []);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error.message}</p>;


    return (
        <>
            <div className="PcPrivacyPolicyNav">
                <div className="PcDescriptionText">HOME / プライバシーポリシー
                </div>

            </div>

            <div className="TEXTAQ">
                プライバシーポリシー
            </div>

            <div className="PcPrivacyPolicy-container">
                <div className="PcPrivacyPolicyInside">
                    <div className="PcPrivacyPolicyInside2">
                        {data}
                    </div>
                </div>
            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcPrivacyPolicy;