import React, { useEffect, useState } from 'react';
import './PcCart.css'
import axios from "axios";
import ProductDetailComponent from "./ProductDetailComponent";
import { useNavigate } from 'react-router-dom';
import filterMoney from '../../../utils/filterMoney';
import APIHelper from '../../../utils/APIHelper';



function PcCart() {
    const navigate = useNavigate();
    const [dataList, setDataList] = useState([]);
    const [TotalValue, setTotalValue] = useState(0);
    const [refreshFlag, setRefreshFlag] = useState(0)


    useEffect(() => {
        APIHelper.getSc().then(response => {
            const { data } = response;
            setDataList(data && data.data || []);
            fetchTotalValue();
        })
            .catch(error => {
                console.error("Error fetching cart data:", error);
            });
    }, [refreshFlag]);


    const fetchTotalValue = () => {
        APIHelper.getScSumAll().then(({ data }) => {
            setTotalValue(data.data || 0);
        })
            .catch(error => {
                console.error("Error fetching cart sum:", error);
            });
    };

    const handleCartOperation = () => {
        setRefreshFlag((val) => val + 1)
    };

    return (
        <>
            <div className="PcCart-container">
                <div className="PcCart-container-inside">
                    <div className="PcCart-container-left">
                        {dataList.map(data => (
                            <ProductDetailComponent key={data.id} productId={data.productId} quantity={data.quantity} onCartOperation={handleCartOperation} />
                        ))}
                    </div>
                    <div className="PcCart-container-right">
                        <div className="PcCart-container-accounts">
                            <div>
                                <div style={{ fontSize: "30px", }}>商品の小計  （税込）：</div> <br />
                                <div style={{ fontSize: "30px" }}>{filterMoney(TotalValue)}</div>
                            </div>
                            <button className="buy-btn" onClick={
                                () => {
                                    navigate('/PcCartsNext')
                                }
                            }>ご購入手続き</button>
                            <button className="continue-btn" onClick={
                                () => {
                                    navigate('/')
                                }
                            }>お買い物を続ける</button>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default PcCart;




