
// ProductsPCFavoritesGrid.js
import React from 'react';
import './ProductsPCFavoritesGrid.css';
import { Link } from "react-router-dom";
import Heart from "../../../../components/Heart";
import filterMoney from '../../../../utils/filterMoney';
const ProductItem = ({ product }) => {
    console.log("product");
    console.log(product);
    return (
        <>
            <div className="grid-item">
                <Heart ProductId={product.id} />
                <Link to={`/product/${product.id}`}>

                    <img src={product.image1} alt={product.name} />
                    <div className="product-info">
                        <div className="product-name">{product.name}</div>
                        <div className="product-price">{filterMoney(product.afterTaxPrice)}</div>
                    </div>
                </Link>
            </div>

        </>

    );
};

const ProductsPCFavoritesGrid = ({ products }) => {
    return (
        <div className="grid-container-favorite">
            {products.map(product => (
                <ProductItem key={product.id} product={product} />
            ))}
        </div>
    );
};

export default ProductsPCFavoritesGrid;

