import React, { useEffect, useState } from 'react';
import ProductsGrid from './ProductsGrid';
import './PCCategory.css'
import { useLocation } from 'react-router-dom';
import APIHelper from '../../../utils/APIHelper';




function PCCategory() {

    const [products, setProducts] = useState([]);
    const [maxDisplay, setMaxDisplay] = useState(20);
    const [currentPage, setCurrentPage] = useState(1);
    const location = useLocation();
    const queryParams = new URLSearchParams(location.search);
    const categoryId = queryParams.get('id');
    useEffect(() => {
        APIHelper.getMoreCategory(categoryId).then((response) => {
            if (response.data && response.data.data) {
                setProducts(response.data.data);
                console.log("response.data.data:", response.data.data)
            }
        })
            .catch((err) => {
                console.error("Error fetching products:", err.message);
            });
    }, [categoryId]);

    const totalPages = Math.ceil(products.length / maxDisplay);

    const getPageRange = (current, total, delta = 2) => {
        const left = current - delta;
        const right = current + delta + 1;
        const range = [];
        const rangeWithDots = [];
        let l;
        for (let i = 1; i <= total; i++) {
            if (i === 1 || i === total || i >= left && i < right) {
                range.push(i);
            }
        }
        for (let i of range) {
            if (l) {
                if (i - l === 2) {
                    rangeWithDots.push(l + 1);
                } else if (i - l !== 1) {
                    rangeWithDots.push('...');
                }
            }
            rangeWithDots.push(i);
            l = i;
        }

        return rangeWithDots;
    };

    const pageRange = getPageRange(currentPage, totalPages);

    const displayedProducts = products.slice((currentPage - 1) * maxDisplay, currentPage * maxDisplay);

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(prev => prev - 1);
        }
    };

    const nextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(prev => prev + 1);
        }
    };


    return (
        <>
            <div className="CategoryNav">
                <div className="CategoryNavText">HOME / CATEGORY / ALL</div>
            </div>
            <div className="All">ALL</div>
            <div className="category-container-select">
                <div className="category-container2-select">
                    <label className="category-select">
                        件数:
                        <select value={maxDisplay} onChange={e => setMaxDisplay(Number(e.target.value))}>
                            {[20, 40, 60].map(option => (
                                <option key={option} value={option}>
                                    {option}
                                </option>
                            ))}
                        </select>
                    </label>
                </div>
            </div>
            <ProductsGrid products={displayedProducts} />
            <div className="category-container-page">
                <button onClick={prevPage} disabled={currentPage === 1}>
                    {"<"}
                </button>

                {pageRange.map((page, index) => {
                    if (typeof page === 'number') {
                        return (
                            <button
                                key={index}
                                onClick={() => setCurrentPage(page)}
                                disabled={currentPage === page}
                                className={currentPage === page ? "selected-page" : ""}
                            >
                                {page}
                            </button>
                        );
                    } else {
                        return (
                            <span key={index} className="pagination-ellipsis">
                                {page}
                            </span>
                        );
                    }
                })}

                <button onClick={nextPage} disabled={currentPage === totalPages}>
                    {">"}
                </button>
            </div>

            <div className="CategoryFooterNav"></div>
        </>
    );
}

export default PCCategory;



