import "./PCFavorites.css";
import React, { useEffect, useMemo, useState } from "react";
import ProductsPCFavoritesGrid from "./ProductsPCFavoritesGrid";
import PcMyPageNav from "../PcMyPageNav/PcMyPageNav";
import axios from "axios";
import APIHelper from "../../../../utils/APIHelper";



function PCFavorites() {


    const [sampleProducts3, setSampleProducts3] = useState([]); // 使用状态而不是外部变量
    const [maxDisplay, setMaxDisplay] = useState(20);
    const [currentPage, setCurrentPage] = useState(1);
    const [isDone, setIsDone] = useState(false);
    const [loadingStatus, setLoadingStatus] = useState("loading");
    useEffect(() => {
        const myToken = sessionStorage.getItem('reborn-ec-token');
        if (!myToken) {
            alert("ログインしてください");
        }
        (async function () {
            const Token = myToken;
            try {
                const response = await APIHelper.getFavoriteList();
                // console.log("-------");
                // console.log(response.data.data);
                await ArrayList(response.data.data);
                setLoadingStatus("loaded");
            } catch (error) {
                setLoadingStatus("error");
                console.error('Error fetching the favorites:', error);
            }
        })();

    }, []);

    async function ArrayList(array) {
        let products = [];

        for (let e of array) {
            try {
                const response = await APIHelper.getProductDetail(e);
                products.push(response.data.data.product);
            } catch (error) {
                console.error('Error fetching the favorites:', error);
            }
        }

        setSampleProducts3(products); // 更新状态
        allDone();
    }

    function allDone() {
        setIsDone(true);
        console.log("終わるました");
    }

    const totalPages = useMemo(() => {
        return Math.ceil(sampleProducts3.length / maxDisplay);
    }, [sampleProducts3, maxDisplay]);


    const getPageRange = (current, total, delta = 2) => {
        if (total <= 1) return [1];


        const range = [];
        for (let i = Math.max(2, current - delta); i <= Math.min(total - 1, current + delta); i++) {
            range.push(i);
        }

        if (current - delta > 2) {
            range.unshift("...");
        }

        if (current + delta < total - 1) {
            range.push("...");
        }

        range.unshift(1);
        range.push(total);

        return range;
    };

    const pageRange = getPageRange(currentPage, totalPages);

    //  const totalPages = Math.ceil(sampleProducts3.length / maxDisplay);

    const nextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(currentPage + 1);
        }
    };

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1);
        }
    };


    const displayedProducts = sampleProducts3.slice((currentPage - 1) * maxDisplay, currentPage * maxDisplay);

    return (<>
        <div className="PCFavoritesNav">
            <div className="CategoryNavText">HOME / BEST SELLER</div>

        </div>
        <div className="All">
            MY PAGE
        </div>
        <div className="PcFavorite-container-out">
            <div className="PcFavorite-container">
                <div className="PcFavorite-left">
                    <PcMyPageNav />
                </div>
                <div className="PcFavorite-right">

                    <div className="MyPage-top-context">お気に入り</div>

                    <div className="PcFavoriteApp">

                        {sampleProducts3.length === 0 && !isDone ? (
                            <div>loading</div>
                        ) : (
                            <>
                                <div className="PcHotShop-container-select">
                                    <div className="PcHotShop-container2-select">
                                        <label className="PcHotShop-select">
                                            表示件数:
                                            <select value={maxDisplay} onChange={e => setMaxDisplay(Number(e.target.value))}>
                                                {[20, 50, 100].map(option => (
                                                    <option key={option} value={option}>
                                                        {option}
                                                    </option>
                                                ))}
                                            </select>
                                        </label>
                                    </div>
                                </div>

                                <ProductsPCFavoritesGrid products={displayedProducts} />

                                <div className="category-container-page">
                                    <button onClick={prevPage} disabled={currentPage === 1}>
                                        {"<"}
                                    </button>
                                    {pageRange.map((page, index) => {
                                        if (typeof page === 'number') {
                                            return (
                                                <button
                                                    key={index}
                                                    onClick={() => setCurrentPage(page)}
                                                    disabled={currentPage === page}
                                                    className={currentPage === page ? "selected-page" : ""}
                                                >
                                                    {page}
                                                </button>
                                            );
                                        } else {
                                            return (
                                                <span key={index} className="pagination-ellipsis">
                                                    {page}
                                                </span>
                                            );
                                        }
                                    })}
                                    <button onClick={nextPage} disabled={currentPage === totalPages}>
                                        {">"}
                                    </button>
                                </div>
                            </>
                        )}
                    </div>

                    <div className="PcHotShopFooterNav"></div>
                </div>
            </div>
        </div>
    </>
    )
}

export default PCFavorites;


