import React, { useEffect, useState } from 'react';
import axios from 'axios';
import './PciTemCarousel.css';
import APIHelper from '../../../utils/APIHelper';

function PciTemCarousel({ setImg }) {
    const url = window.location.href;
    const parts = url.split('/');
    const productId = parts[parts.length - 1];
    const [images, setImages] = useState([]);
    const [currentIndex, setCurrentIndex] = useState(0);

    useEffect(() => {
        const imageProduct = async () => {
            try {
                const response = await APIHelper.getProductDetail(productId);
                const data = response.data;
                if (data.code) {
                    const tempImages = [
                        data.data.product.image1, data.data.product.image2,
                        data.data.product.image3, data.data.product.image4,
                        data.data.product.image5, data.data.product.image6,
                        data.data.product.image7, data.data.product.image8,
                        data.data.product.image9, data.data.product.image10
                    ].filter(Boolean);
                    setImages(tempImages);
                }
            } catch (err) {
                console.log(err);
            }
        };
        imageProduct();
    }, [productId]);

    const nextSlide = () => {
        setCurrentIndex((prevIndex) => (prevIndex + 3 < images.length ? prevIndex + 3 : prevIndex));
    };

    const prevSlide = () => {
        setCurrentIndex((prevIndex) => (prevIndex - 3 >= 0 ? prevIndex - 3 : prevIndex));
    };

    return (
        <>
            <div className="PciTemCarousel2-carousel-container">
                <button onClick={prevSlide} disabled={currentIndex === 0}>
                    <svg t="1695563977769" className="icon PciTemCarousel2-btn" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="4891" width="200" height="200">
                        <path
                            d="M410.7 512.4L749.1 174c24.9-24.9 24.9-65.6 0-90.5-24.9-24.9-65.6-24.9-90.5 0L274.9 467.2c-6.2 6.2-10.9 13.4-14 21.1-5.4 13.5-6.1 28.5-2 42.4 0.6 2 1.3 3.9 2 5.9 3.1 7.7 7.8 14.9 14 21.1l383.6 383.6c24.9 24.9 65.6 24.9 90.5 0 24.9-24.9 24.9-65.6 0-90.5L410.7 512.4z"
                            p-id="4892"></path>
                    </svg>
                </button>
                <div className="PciTemCarousel2-images-display">
                    {images.slice(currentIndex, currentIndex + 3).map((image, idx) => (
                        <img key={idx} src={image} alt={`carousel-item ${idx}`} className="PciTemCarousel2-carousel-image" onClick={() => setImg(image)} />
                    ))}
                </div>
                <button className="PciTemCarousel2-btn" onClick={nextSlide} disabled={currentIndex + 3 >= images.length}>
                    <svg t="1695563997693" className="icon PciTemCarousel2-btn" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="5169" width="200" height="200">
                        <path
                            d="M613.3 512.4L274.9 850.8c-24.9 24.9-24.9 65.6 0 90.5 24.9 24.9 65.6 24.9 90.5 0L749 557.7c6.2-6.2 10.9-13.4 14-21.1 5.4-13.5 6.1-28.5 2-42.4-0.6-2-1.3-3.9-2-5.9-3.1-7.7-7.8-14.9-14-21.1L365.4 83.5c-24.9-24.9-65.6-24.9-90.5 0-24.9 24.9-24.9 65.6 0 90.5l338.4 338.4z"
                            p-id="5170"></path>
                    </svg></button>
            </div>
        </>
    );
}

export default PciTemCarousel;



