import React, { useEffect } from 'react';
import axios from "axios";

import Carousel from "./components/Carousel/Carousel";
import BestSeller from "./components/BestSeller/BestSeller"
import Category from "./components/CATEGORY/Category";
import Service from "./components/Service/Service";
import './Home.css'
import APIHelper from '../../../utils/APIHelper';



function Home() {

    useEffect(() => {
        axios.get("https://reborn-domain-mapping.s3.ap-northeast-1.amazonaws.com/reborn-domain-mapping.json").then(res => console.log(res))
    }, [])

    const getIP = async () => {
        const sessionIp = sessionStorage.getItem('my_ip')
        if (sessionIp) {
            return sessionIp
        } else {
            try {
                const res = await axios.get("https://api.ipify.org/?format=json");
                console.log(res.data);
                const ip = res.data.ip || "";
                sessionStorage.setItem('my_ip', ip)
                return ip
            } catch (error) {
                return ''
            }

        }

    };
    useEffect(() => {
        getIP().then(ip => {
            const email = sessionStorage.getItem("reborn-ec-email") || '';
            APIHelper.getUserAccess({
                email,
                ip
            }).then(res => console.log(res))
        })
    }, [])
    return (
        <>
            <Carousel />
            <BestSeller />
            <Category />
            <Service />
        </>
    )
}

export default Home;