import React, { useEffect, useState } from "react";
import './PcSearchAnswer.css';
import ProductsGrid from './ProductsGrid';
import { useLocation } from "react-router-dom";
import APIHelper from "../../../utils/APIHelper";


function PcSearchAnswer() {
    const [SearchProducts, setSearchProducts] = useState([]);
    const [maxDisplay, setMaxDisplay] = useState(20);
    const [currentPage, setCurrentPage] = useState(1);

    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const searchValue = searchParams.get("search");

    useEffect(() => {
        APIHelper.getSearch(searchValue).then((response) => {
            if (response.data && response.data.data) {

                setSearchProducts(Object.values(response.data.data).map(item => item.product));
            }
        })
            .catch((err) => {
                console.error("Error fetching products:", err.message);
            });
    }, [searchValue]);

    const totalPages = Math.ceil(SearchProducts.length / maxDisplay);

    const getPageRange = (current, total, delta = 2) => {
        const left = current - delta;
        const right = current + delta + 1;
        const range = [];
        const rangeWithDots = [];
        let l;

        for (let i = 1; i <= total; i++) {
            if (i === 1 || i === total || i >= left && i < right) {
                range.push(i);
            }
        }

        for (let i of range) {
            if (l) {
                if (i - l === 2) {
                    rangeWithDots.push(l + 1);
                } else if (i - l !== 1) {
                    rangeWithDots.push('...');
                }
            }
            rangeWithDots.push(i);
            l = i;
        }

        return rangeWithDots;
    };

    const pageRange = getPageRange(currentPage, totalPages);

    const displayedProducts = SearchProducts.slice((currentPage - 1) * maxDisplay, currentPage * maxDisplay);

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(prev => prev - 1);
        }
    };

    const nextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(prev => prev + 1);
        }
    };

    return (
        <>
            <div className="searchNav">
                <div className="searchNavText">HOME / SEARCH</div>
            </div>
            <div className="All">検索結果一覧</div>
            <div className="search-container-select">
                <div className="search-container2-select">
                    <label className="search-select">
                        表示件数:
                        <select value={maxDisplay} onChange={e => setMaxDisplay(Number(e.target.value))}>
                            {[20, 40, 60].map(option => (
                                <option key={option} value={option}>
                                    {option}
                                </option>
                            ))}
                        </select>
                    </label>
                </div>
            </div>
            <ProductsGrid products={displayedProducts} />
            <div className="search-container-page">
                <button onClick={prevPage} disabled={currentPage === 1}>
                    {"<"}
                </button>
                {pageRange.map((page, index) => {
                    if (typeof page === 'number') {
                        return (
                            <button
                                key={index}
                                onClick={() => setCurrentPage(page)}
                                disabled={currentPage === page}
                                className={currentPage === page ? "selected-page" : ""}
                            >
                                {page}
                            </button>
                        );
                    } else {
                        return (
                            <span key={index} className="pagination-ellipsis">
                                {page}
                            </span>
                        );
                    }
                })}
                <button onClick={nextPage} disabled={currentPage === totalPages}>
                    {">"}
                </button>
            </div>
            <div className="searchFooterNav"></div>
        </>
    );
}

export default PcSearchAnswer;
