import React, { useEffect, useState } from 'react';
import './Category.css'
import { Link } from "react-router-dom";
import axios from "axios";
import Heart from "../../../../../components/Heart";
import filterMoney from '../../../../../utils/filterMoney';
import APIHelper from '../../../../../utils/APIHelper';
const styles = {
    container: {
        display: 'flex',
        flexWrap: 'wrap',
        width: (256 + 32) * 4,  // 图片宽度 + 间隔，乘以每行的4个产品
        justifyContent: 'center',
        margin: 'auto',
        gap: '32px'
    },
    image: {
        width: '256px',
        height: '256px',
        objectFit: 'cover',
    },
    item: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'flex-start',
        width: '256px',
    },
    productName: {
        display: '-webkit-box',
        '-webkit-line-clamp': 2,
        '-webkit-box-orient': 'vertical',
        overflow: 'hidden',
        textOverflow: 'ellipsis',
        width: '256px',
        height: '45px',
        marginTop: '10px',
    },
    price: {
        marginTop: '5px',
    }
}

function Category() {

    const [SelectedID, setSelectedID] = useState(0);
    const [products, setProducts] = useState([]);
    const [selected, setSelected] = useState(null);
    const [categories, setCategories] = useState([
        { label: 'ALL', id: 0 },
        { label: 'CATEGORY 1', id: 1 },
        { label: 'CATEGORY 2', id: 2 },
        { label: 'CATEGORY 3', id: 3 }
    ]);


    useEffect(() => {
        // Fetch the data when the component mounts or SelectedID changes
        APIHelper.getProductsCategory(SelectedID).then(response => {
            setProducts(response.data.data);
        }).catch(error => {
            console.error('Error fetching the products:', error);
        });
    }, [SelectedID]);

    useEffect(() => {
        Promise.all([APIHelper.getCategory(1), APIHelper.getCategory(2), APIHelper.getCategory(3)]).then((res) => {
            const [res1, res2, res3] = res;
            const categories = [
                { label: 'ALL', id: 0 },
                { label: res1.data.data, id: 1 },
                { label: res2.data.data, id: 2 },
                { label: res3.data.data, id: 3 }
            ];
            setCategories(categories)
        })
    }, [])

    return (
        <>
            <div className="category-container">
                <div className="category-container-context">
                    CATEGORY
                </div>


                <div className="CATEGORY-button">
                    {categories.map(category => (
                        <button
                            key={category.id}
                            className={selected === category.label ? 'selected' : ''}

                            onMouseEnter={() => {
                                setSelected(category.label);
                                setSelectedID(category.id);
                                //  categoryChange();
                            }}
                        >
                            {category.label}
                        </button>
                    ))}
                </div>
                <div style={styles.container}>
                    {products.map((product, index) => (
                        <div className="grid-item LinkBtnAnimaPCHomePageCategory">
                            <Heart ProductId={product.id} />
                            <Link to={`/product/${product.id}`} >
                                <div className="productShower" key={index} style={styles.item}>
                                    <img src={product.image1} alt={`img-${index}`} style={styles.image} />
                                    <div style={styles.productName}>{product.name}</div>
                                    <div style={styles.price}>{`${filterMoney(product.afterTaxPrice)}`}</div>
                                </div>
                            </Link>
                        </div>
                    ))}
                </div>
            </div>
            <div className="viewMore-container">
                <Link to={`./PCCategory?id=${SelectedID}`} className="custom-link">
                    <div className="viewMore">
                        VIEW MORE
                    </div>
                </Link>
            </div>
        </>
    )
}

export default Category;





