import React, { useState, useEffect, useRef } from 'react';
import './Navbar.css';
import { Link, useNavigate, useLocation } from "react-router-dom";
import SearchInput from "../../pages/PC/PcHome/components/SearchInput";
import axios from "axios";
import APIHelper from '../../utils/APIHelper';




function Navbar() {
    const [showMenu, setShowMenu] = useState(false);
    const [email, setEmail] = useState(null);
    const navigate = useNavigate();
    const [logoImage, setLogoImage] = useState('false');
    const buttonRef = useRef(null);
    const menuRef = useRef(null);
    const location = useLocation()

    useEffect(() => {
        const myToken = sessionStorage.getItem('reborn-ec-token');
        // Mock checking for token. In real life, you would likely fetch this from an API.
        if (myToken) {
            APIHelper.getUserDetail().then(response => {
                console.log('Data sent successfully:', response.data.data);
                setEmail(response.data.data.userId);
                console.log(email);
            }).catch(error => {
                console.error('Error sending data:', error);
            });
        } else {
            console.error('No data found in sessionStorage');
        }

        APIHelper.getLogo().then(response => {
            console.log('LOGO获取成功:', response.data);
            setLogoImage(response.data.data);
        }).catch(error => {
            console.error('Error fetching shop logo data:', error);
        });


    }, [location]);


    const handleMouseLeave = (event) => {
        if (event.relatedTarget instanceof Node) {
            if (
                buttonRef.current && !buttonRef.current.contains(event.relatedTarget) &&
                menuRef.current && !menuRef.current.contains(event.relatedTarget)
            ) {
                setShowMenu(false);
            }
        }
    };

    const closeMenu = () => {
        setShowMenu(false);
    };

    const handleSearch = (value) => {
        navigate(`/PcSearchAnswer?search=${value}`);
        console.log("User searched for:", value);
    };

    const handleToshop = () => {
        const myToken = sessionStorage.getItem('reborn-ec-token');
        if (myToken) {
            navigate(`/PcCarts`);
        } else {
            navigate(`/PcAccount`);

        }
    }



    return (

        <>
            <div className="navbar-container">

                <div className="navbar" ref={menuRef} onMouseLeave={handleMouseLeave}>
                    <button ref={buttonRef}
                        onMouseEnter={() => setShowMenu(true)}>
                        <svg t="1692988911558" className="icon LinkCartBtnAnima" viewBox="0 0 1024 1024" version="1.1"
                            xmlns="http://www.w3.org/2000/svg" p-id="4005" width="200" height="200">
                            <path d="M112 240h800v48H112v-48z m0 240h800v48H112v-48z m0 256h800v48H112v-48z" p-id="4006"></path>
                        </svg>
                    </button>

                    <div className="logo">
                        <Link to={"./"}>
                            {logoImage ? <img className="logoImg" src={logoImage} alt="Logo" /> : <img className="logoImg" src={process.env.PUBLIC_URL + '/assets/img/PC/PcHome/logo.jpg'} alt="Logo" />}
                            {/*<img className="logoImg" src={process.env.PUBLIC_URL + '/assets/img/PC/PcHome/logo.jpg'} alt="Logo"/>*/}</Link>
                    </div>

                    <div className="icons">
                        {/* 搜索 */}
                        <div className="nav-bar-search ">
                            <svg className="nav-bar-search-icon LinkCartBtnAnima" t="1692991710782" viewBox="0 0 1024 1024" version="1.1"
                                xmlns="http://www.w3.org/2000/svg" p-id="4980" width="200" height="200">
                                <path
                                    d="M966.4 924.8l-230.4-227.2c60.8-67.2 96-156.8 96-256 0-217.6-176-390.4-390.4-390.4-217.6 0-390.4 176-390.4 390.4 0 217.6 176 390.4 390.4 390.4 99.2 0 188.8-35.2 256-96l230.4 227.2c9.6 9.6 28.8 9.6 38.4 0C979.2 950.4 979.2 934.4 966.4 924.8zM102.4 441.6c0-185.6 150.4-339.2 339.2-339.2s339.2 150.4 339.2 339.2c0 89.6-35.2 172.8-92.8 233.6-3.2 0-3.2 3.2-6.4 3.2-3.2 3.2-3.2 3.2-3.2 6.4-60.8 57.6-144 92.8-233.6 92.8C256 780.8 102.4 627.2 102.4 441.6z"
                                    p-id="4981">
                                </path>
                            </svg>
                            <SearchInput onEnterPressed={handleSearch} onBlur={handleSearch} />
                        </div>


                        {/* 购物车*/}
                        <div onClick={handleToshop}>
                            <svg className="icon LinkCartBtnAnima" t="1692991768454" viewBox="0 0 1024 1024" version="1.1"
                                xmlns="http://www.w3.org/2000/svg" p-id="5990" width="200" height="200">
                                <path d="M246.4 912a2.1 2.1 0 1 0 134.4 0 2.1 2.1 0 1 0-134.4 0Z" p-id="5991"></path>
                                <path d="M716.8 912a2.1 2.1 0 1 0 134.4 0 2.1 2.1 0 1 0-134.4 0Z" p-id="5992"></path>
                                <path
                                    d="M905.6 764.8l-537.6 0c-28.8 0-57.6-25.6-64-54.4l-96-566.4c-9.6-54.4-60.8-96-115.2-96l-22.4 0c-12.8 0-25.6 12.8-25.6 25.6 0 12.8 12.8 25.6 25.6 25.6l22.4 0c28.8 0 57.6 25.6 64 54.4l96 566.4c9.6 54.4 60.8 96 115.2 96l537.6 0c12.8 0 25.6-12.8 25.6-25.6C931.2 777.6 921.6 764.8 905.6 764.8z"
                                    p-id="5993"></path>
                                <path
                                    d="M880 179.2l-572.8 0c-12.8 0-25.6 12.8-25.6 25.6 0 12.8 12.8 25.6 25.6 25.6l572.8 0c25.6 0 38.4 16 32 41.6l-70.4 281.6c-6.4 32-41.6 57.6-73.6 60.8l-396.8 28.8c-12.8 0-25.6 12.8-22.4 28.8 0 12.8 12.8 25.6 28.8 22.4l396.8-28.8c54.4-3.2 105.6-48 118.4-99.2l70.4-281.6C976 230.4 937.6 179.2 880 179.2z"
                                    p-id="5994"></path>
                            </svg>
                        </div>

                    </div>


                </div>
            </div>

            {showMenu && (
                <div className="navbar-container-menu" ref={menuRef} onMouseLeave={handleMouseLeave}>
                    <div className="navbar-container-mid-menu">
                        <div className="menu" onClick={closeMenu} >
                            {
                                email
                                    ? <span style={{
                                        backgroundColor: "#F57948",
                                        borderRadius: "10px",
                                    }} className="link" to="PcAccount">{email} 様</span>
                                    : <Link style={{
                                        backgroundColor: "#F57948",
                                        borderRadius: "10px",
                                    }} className="link" to="/PcAccount">ログイン/会員登録</Link>
                            }
                            <Link className="link" to="/">HOME</Link>
                            {/*<Link className="link" to="/PCCategory">BestSeller</Link>*/}
                            <Link className="link" to="/PCCategory?id=0">CATEGORY</Link>
                            <Link className="link" to="/MyPage">MY PAGE</Link>
                        </div>
                    </div>
                </div>
            )}
        </>

    );
}

export default Navbar;