import React, { useState, useEffect } from 'react';
import QRCode from 'qrcode.react';
import { useLocation, useNavigate } from 'react-router-dom';

import './PcWeChatPay.css'
import APIHelper from '../../../../utils/APIHelper';
// const { Title } = Typography;

const PcWeChatPay = (props) => {

    const navigate = useNavigate();
    const location = useLocation();
    const settleData = location.state && location.state.settleData;
    const requestBody = {
        email: settleData.email,
        orderId: settleData.orderId
    }


    // const testRequestBody = {
    //     email: "deng2350@gmail.com",
    //     orderId: "a58b2274-ca95-41d8-869f-9b63f9af23f4"
    // }

    const [qrCodeValue, setQRCodeValue] = useState('');
    const [chargeId, setChargeId] = useState('');
    const [qrCodeImg, setQRCodeImg] = useState('')


    useEffect(() => {
        async function fetchData() {
            await APIHelper.wxPay(requestBody).then(res => {
                console.log("FUCK FUCK:", res.data.data.chargeId)
                setQRCodeImg(res.data.data.qrImage)
                setQRCodeValue(res.data.data.qrImage)
                setChargeId(res.data.data.chargeId)
            }).catch(error => {
                console.error("Error fetching data:", error);
            });
        }

        fetchData();

    }, []);

    useEffect(() => {
        let timeCount = 0;

        const pollChargeStatus = () => {
            APIHelper.getWxPay(chargeId).then(res => {
                console.log("duck", res.data);
                if (res.data.data == 'successful') {

                }
                if (timeCount < 10) {
                    setTimeout(pollChargeStatus, 10000);
                }
            }).catch(err => {
                console.log(err);
            });
            timeCount++;
            console.log('Fuck ' + timeCount);
        };

        pollChargeStatus();
    }, [])



    return (
        <>
            <div className="PcWechatPay-container">
                <div className="PcWechatPay-text">
                    <svg t="1695570175556" className="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="4871" width="200" height="200">
                        <path
                            d="M395.846 603.585c-3.921 1.98-7.936 2.925-12.81 2.925-10.9 0-19.791-5.85-24.764-14.625l-2.006-3.864-78.106-167.913c-0.956-1.98-0.956-3.865-0.956-5.845 0-7.83 5.928-13.68 13.863-13.68 2.965 0 5.928 0.944 8.893 2.924l91.965 64.43c6.884 3.864 14.82 6.79 23.708 6.79 4.972 0 9.85-0.945 14.822-2.926L861.71 282.479c-77.149-89.804-204.684-148.384-349.135-148.384-235.371 0-427.242 157.158-427.242 351.294 0 105.368 57.361 201.017 147.323 265.447 6.88 4.905 11.852 13.68 11.852 22.45 0 2.925-0.957 5.85-2.006 8.775-6.881 26.318-18.831 69.334-18.831 71.223-0.958 2.92-2.013 6.79-2.013 10.75 0 7.83 5.929 13.68 13.865 13.68 2.963 0 5.928-0.944 7.935-2.925l92.922-53.674c6.885-3.87 14.82-6.794 22.756-6.794 3.916 0 8.889 0.944 12.81 1.98 43.496 12.644 91.012 19.53 139.48 19.53 235.372 0 427.24-157.158 427.24-351.294 0-58.58-17.78-114.143-48.467-163.003l-491.39 280.07-2.963 1.98z"
                            fill="#09BB07" p-id="4872"></path>
                    </svg>
                    <h2>微信支付</h2>
                </div>
                <div className="PcWechatPay-container-center">
                    { /*qrCodeValue ? <QRCode className="PcWechatPay-container-center-QRCode" value={qrCodeValue} /> : <h2>Waiting</h2> */}
                    {qrCodeImg ? <img src={qrCodeImg} alt="Image" /> : <h2>Waiting</h2>}
                </div>
            </div>
        </>
    );
};

export default PcWeChatPay;