import React, { useEffect, useMemo, useState } from "react";
import './PcShopHistory.css';
import PcMyPageNav from "../PcMyPage/PcMyPageNav/PcMyPageNav";
import axios from "axios";
import filterMoney from "../../../utils/filterMoney";
import APIHelper from "../../../utils/APIHelper";

const myToken = sessionStorage.getItem('reborn-ec-token');


function PcShopHistory() {

    const [data, setData] = useState([]);
    const maxDisplay = 21;
    const [currentPage, setCurrentPage] = useState(1);


    useEffect(() => {

        if (!myToken) {
            alert("まずログインしてください");
            return;
        }

        const fetchPaymentHistory = async () => {
            try {
                const response = await APIHelper.getHistory();
                console.log("***********")
                console.log(response.data)

                return response.data.data.paymentViews || [];
            } catch (err) {
                console.log(err);
                return [];
            }
        };

        fetchPaymentHistory()
            .then(paymentViews => {
                const allItems = paymentViews.flatMap(paymentView => paymentView.items);
                setData(allItems);
                console.log(allItems);
            });

    }, [myToken]);



    const totalPages = useMemo(() => {
        return Math.ceil(data.length / maxDisplay);
    }, [data]);

    const displayedData = data.slice((currentPage - 1) * maxDisplay, currentPage * maxDisplay);

    const nextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(currentPage + 1);
        }
    };

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(currentPage - 1);
        }
    };





    return (
        <>
            <div className="PcShopHistoryNav">
                <div className="PcShopHistoryText">HOME / MY PAGE / 購入履歴</div>
            </div>
            <div className="TEXTShopHistory">
                MY PAGE
            </div>

            <div className="PcShopHistory-container-outer">
                <div className="PcShopHistory-container">
                    <div className="PcShopHistory-left">


                        <PcMyPageNav />

                    </div>
                    <div className="PcShopHistory-right">
                        <div className="PcShopHistory-top-context">購入商品一覧</div>

                        <div>
                            <div style={{
                                width: "100%",
                                height: "20px",
                            }}></div>
                            {/*{data.map((item, index) => (*/}
                            {/*    <div key={index} className="PcShopHistory-items-container">*/}

                            {/*        <div className="PcShopHistory-items-container-left">*/}
                            {/*            <img src={item.productImg} alt=""/>*/}
                            {/*        </div>*/}

                            {/*        <div className="PcShopHistory-items-container-right">*/}
                            {/*            <p>{item.productName}</p>*/}
                            {/*            <p><span>¥</span>{item.price} <span>(税込）</span> </p>*/}
                            {/*            <p><span>品番:</span>{item.productId}</p>*/}
                            {/*            <p><span>購入数量:</span>{item.quantity}</p>*/}
                            {/*            <p><span>購入日期:</span>{item.updatedAt}</p>*/}
                            {/*        </div>*/}

                            {/*    </div>*/}
                            {/*))}*/}
                            <div>
                                {displayedData.map((item, index) => (
                                    <div key={index} className="PcShopHistory-items-container">
                                        <div className="PcShopHistory-items-container-left">
                                            <img src={item.productImg} alt="" />
                                        </div>

                                        <div className="PcShopHistory-items-container-right">
                                            <p>{item.productName}</p>
                                            <p>
                                                {filterMoney(item.price)} <span>(税込）</span>{" "}
                                            </p>
                                            <p>
                                                <span>品番:</span>{item.productId}
                                            </p>
                                            <p>
                                                <span>購入数量:</span>{item.quantity}
                                            </p>
                                            <p>
                                                <span>購入日期:</span>{item.updatedAt.match(/^(\d{4}-\d{2}-\d{2})/)[1]}
                                            </p>
                                        </div>
                                    </div>
                                ))}

                                <div style={{
                                    display: "flex",
                                    justifyContent: "center",
                                    marginTop: "5px"
                                }}>
                                    <button onClick={prevPage} disabled={currentPage === 1}>
                                        {"<"}
                                    </button>
                                    {Array.from({ length: totalPages }).map((_, index) => {
                                        const page = index + 1;

                                        return (
                                            <button
                                                key={page}
                                                onClick={() => setCurrentPage(page)}
                                                disabled={currentPage === page}
                                                className={currentPage === page ? "selected-page" : ""}
                                            >
                                                {page}
                                            </button>
                                        );
                                    })}
                                    <button onClick={nextPage} disabled={currentPage === totalPages}>
                                        {">"}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )

}



export default PcShopHistory;