import React, { useEffect, useState } from "react";
import './PcCompany.css'
import axios from "axios";
import APIHelper from "../../../../utils/APIHelper";


function PcCompany() {

    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {

        APIHelper.getProfile()
            .then((response) => {
                setData(response.data.data);
                setLoading(false);
            })
            .catch((err) => {
                setError(err);
                setLoading(false);
            });
    }, []);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error.message}</p>;

    return (
        <>
            <div className="PcCompanyNav">
                <div className="PcCompanyText">HOME / 会社概要</div>

            </div>
            <div className="TEXTAQ">
                会社概要
            </div>

            <div className="PcCompany-container">
                <div className="PcCompanyInside">
                    <div className="PcCompanyInside2">
                        {data}
                    </div>
                </div>
            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcCompany;