import React, { useEffect, useState } from "react";
import './PCReceipt.css';
import PcMyPageNav from "../PcMyPage/PcMyPageNav/PcMyPageNav";
import { useNavigate } from 'react-router-dom';
import filterMoney from "../../../utils/filterMoney";
import APIHelper from "../../../utils/APIHelper";

function PCReceipt() {
    const [paymentHistory, setPaymentHistory] = useState([]);
    const [currentPage, setCurrentPage] = useState(1);
    const [maxDisplay, setMaxDisplay] = useState(20);

    const myToken = sessionStorage.getItem('reborn-ec-token');
    const navigate = useNavigate();

    useEffect(() => {
        const fetchPaymentHistory = async () => {
            try {
                const response = await APIHelper.getHistory();
                setPaymentHistory(response.data.data.paymentViews);
            } catch (error) {
                console.error("Error fetching payment history: ", error);
            }
        };

        fetchPaymentHistory();
    }, []);

    const totalPages = Math.ceil(paymentHistory.length / maxDisplay);
    const displayedPayments = paymentHistory.slice((currentPage - 1) * maxDisplay, currentPage * maxDisplay);


    const getPageRange = (current, total, delta = 2) => {
        const left = current - delta;
        const right = current + delta + 1;
        const range = [];
        const rangeWithDots = [];
        let l;
        // 设置页码
        for (let i = 1; i <= total; i++) {
            if (i === 1 || i === total || i >= left && i < right) {
                range.push(i);
            }
        }


        for (let i of range) {
            if (l) {
                if (i - l === 2) {
                    rangeWithDots.push(l + 1);
                } else if (i - l !== 1) {
                    rangeWithDots.push('...');
                }
            }
            rangeWithDots.push(i);
            l = i;
        }

        return rangeWithDots;
    };

    const pageRange = getPageRange(currentPage, totalPages);

    const prevPage = () => {
        if (currentPage > 1) {
            setCurrentPage(prev => prev - 1);
        }
    };

    const nextPage = () => {
        if (currentPage < totalPages) {
            setCurrentPage(prev => prev + 1);
        }
    };

    const renderProductNames = (items) => {
        const displayedItems = items.length > 3 ? items.slice(0, 3) : items;
        const needEllipsis = items.length > 3;

        return (
            <>
                {displayedItems.map((item, index) => (
                    <span key={index}>
                        {item.productName},
                    </span>
                ))}
                {needEllipsis && <span>...</span>}
            </>
        );
    }


    const handleButtonClick = (e) => {
        console.log(e.payment.id);
        navigate('/receiptNext/' + e.payment.id);
    }


    return (
        <>
            <div className="MyPageNav">
                <div className="MyPageText">HOME / MY PAGE / 領収書一覧 </div>
            </div>
            <div className="TEXTMyPage">
                MY PAGE
            </div>

            <div className="PCReceipt-container-nav">

                <div className="PCReceipt-container1-nav">


                    <div className="PCReceipt-container2-nav-left">


                        <PcMyPageNav />
                    </div>

                    <div className="PCReceipt-container2-nav-right">
                        <div className="payment-history-container">
                            <div style={{ width: "824px", height: "40px", backgroundColor: "#F4F4F4", color: "#1A1A1C", fontSize: "20px", lineHeight: "40px", paddingLeft: "10px" }}>領収書一覧</div>

                            <div className="payment-history-item1" style={{ color: "#626264", borderBottom: "1px solid #626264" }}>

                                <div>日付</div>
                                <div>内容</div>
                                <div>合計金額（税込）</div>
                                <div>詳細</div>
                            </div>
                            {displayedPayments.map((paymentView, index) => (
                                <div key={index} className="payment-history-item1" style={{ marginTop: "16px" }}>

                                    <div>{new Date(paymentView.payment.createdAt).toLocaleDateString()}</div>
                                    <div>
                                        {renderProductNames(paymentView.items)}
                                    </div>

                                    <div>{filterMoney(paymentView.totalPriceAfterTax)}</div>
                                    <button style={{ color: "#F57948", width: "160px", height: "40px", border: "1px solid #F57948" }}
                                        onClick={() => handleButtonClick(paymentView)}
                                    >詳細を見る</button>
                                </div>
                            ))}
                            <div className="page-container">
                                <div className="pagination-container">
                                    <button onClick={prevPage} disabled={currentPage === 1}>
                                        {"<"}
                                    </button>

                                    {pageRange.map((page, index) => {
                                        if (typeof page === 'number') {
                                            return (
                                                <button
                                                    key={index}
                                                    onClick={() => setCurrentPage(page)}
                                                    disabled={currentPage === page}
                                                    className={currentPage === page ? "selected-page" : ""}
                                                >
                                                    {page}
                                                </button>
                                            );
                                        } else {
                                            return (
                                                <span key={index} className="pagination-ellipsis">
                                                    {page}
                                                </span>
                                            );
                                        }
                                    })}

                                    <button onClick={nextPage} disabled={currentPage === totalPages}>
                                        {">"}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div style={{ width: "100%", height: "200px" }}></div>

        </>
    )
}

export default PCReceipt;

