
import React, { useEffect, useState } from "react";
import axios from "axios";
import './Heart.css'
import APIHelper from "../utils/APIHelper";

function HeartIcon({ ProductId }) {
    const [IsHeartList, setIsHeartList] = useState([]);

    useEffect(() => {
        (async function () {
            try {
                const response = await APIHelper.getFavoriteList();
                setIsHeartList(response.data.data || []);
            } catch (error) {
                console.error('Error fetching 喜欢', error);
            }
        })();

    }, []);


    const toggleHeart = async (e) => {
        e.stopPropagation();
        const isFavorite = IsHeartList && IsHeartList.find(e => e === ProductId);
        try {
            await APIHelper.handleFavorite(ProductId, isFavorite);
            setIsHeartList(prevList => isFavorite ? prevList.filter(e => e !== ProductId) : [...prevList, ProductId]);
        } catch (error) {
            console.error(isFavorite ? '不喜欢失败' : '喜欢失败', error);
        }
    };





    return (
        <div className={"heart-icon"} onClick={toggleHeart}>

            {IsHeartList && IsHeartList.find(e => e === ProductId) ? <svg t="1693817562439" className="icon" viewBox="0 0 1024 1024" version="1.1"
                xmlns="http://www.w3.org/2000/svg" p-id="4175" width="200" height="200">
                <path
                    d="M512 901.746939c-13.583673 0-26.122449-4.179592-37.093878-13.061225-8.881633-7.314286-225.697959-175.020408-312.424489-311.379592C133.746939 532.37551 94.040816 471.24898 94.040816 384.522449c0-144.718367 108.146939-262.269388 240.326531-262.269388 67.395918 0 131.657143 30.82449 177.632653 84.636735 45.453061-54.334694 109.191837-84.636735 177.110204-84.636735 132.702041 0 240.326531 117.55102 240.326531 262.269388 0 85.159184-37.093878 143.673469-67.395919 191.216327l-1.044898 1.567346c-86.726531 136.359184-303.542857 304.587755-312.424489 311.379592-10.44898 8.359184-22.987755 13.061224-36.571429 13.061225z"
                    fill="#E5404F" p-id="4176"></path>
            </svg> : <svg t="1693817549044" className="icon" viewBox="0 0 1024 1024" version="1.1"
                xmlns="http://www.w3.org/2000/svg" p-id="4039" width="200" height="200">
                <path
                    d="M512 901.746939c-13.583673 0-26.122449-4.179592-37.093878-13.061225-8.881633-7.314286-225.697959-175.020408-312.424489-311.379592C133.746939 532.37551 94.040816 471.24898 94.040816 384.522449c0-144.718367 108.146939-262.269388 240.326531-262.269388 67.395918 0 131.657143 30.82449 177.632653 84.636735 45.453061-54.334694 109.191837-84.636735 177.110204-84.636735 132.702041 0 240.326531 117.55102 240.326531 262.269388 0 85.159184-37.093878 143.673469-67.395919 191.216327l-1.044898 1.567346c-86.726531 136.359184-303.542857 304.587755-312.424489 311.379592-10.44898 8.359184-22.987755 13.061224-36.571429 13.061225zM334.367347 164.04898c-109.714286 0-198.530612 98.742857-198.530612 220.473469 0 74.187755 33.959184 127.477551 61.648979 170.318367 83.069388 130.089796 294.138776 294.138776 303.020408 300.930613 3.657143 2.612245 7.314286 4.179592 11.493878 4.179591s7.836735-1.567347 11.493878-4.179591c8.881633-6.791837 219.95102-170.840816 303.020408-300.930613l1.044898-1.567347c28.212245-44.930612 60.604082-95.608163 60.604081-168.75102 0-121.208163-89.338776-220.473469-198.530612-220.473469-60.081633 0-115.983673 29.257143-153.6 80.979591l-6.269388 8.881633c-4.179592 5.22449-10.44898 8.359184-16.718367 8.359184s-13.061224-3.134694-16.718367-8.359184l-6.269388-8.881633C450.35102 193.828571 393.926531 164.04898 334.367347 164.04898z"
                    fill="#333333" p-id="4040"></path>
            </svg>}
        </div>
    );
}


export default HeartIcon;



