import "./PcPost.css";
import React, { useEffect, useState } from "react";
import axios from 'axios';
import APIHelper from "../../../../utils/APIHelper";

const myToken = sessionStorage.getItem('reborn-ec-token');
function PcPost() {
    const [deliveryInfo, setDeliveryInfo] = useState({
        name: "",
        phone: "",
        postCode: "",
        prefecture: "",
        city: "",
        street: "",
        buildingName: "",
    });
    const [isEditable, setIsEditable] = useState(false);
    const [error, setError] = useState(null);

    useEffect(() => {
        APIHelper.getUserDelivery().then(response => {
            setDeliveryInfo(response.data.data);
        })
            .catch(err => {
                setError(err);
                console.log(error);
            });
    }, []);

    const handleInputChange = (event, field) => {
        const value = event.target.value;
        setDeliveryInfo(prevState => ({ ...prevState, [field]: value }));
    };

    // AddressUpdate
    const updateDeliveryInfo = () => {
        APIHelper.deliveryModify(deliveryInfo).then(response => {
            alert("成功しました");
            console.log(response);

        })
            .catch(err => {
                setError(err);
            });
    }




    return (
        <>
            <div className="PcPostNav">
                <div className="PcPostNavText">HOME / MYPAGE / お届け先</div>

            </div>
            <div className="All">
                お届け先
            </div>
            <div className="PcPost-container">
                <div className="PcPost-container2">
                    <div className="PcAQ-container">
                        <div className="PcAQInside">
                            <div className="PcAQInside2">

                                <p className="PcPost-Text">取引先名称または氏名</p>

                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.name ? deliveryInfo.name : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "name")}
                                    placeholder="取引先名称または氏名"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "お名前"}
                                />

                                <p className="PcPost-Text">電話番号 半角数字・ハイフン不要</p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.phone ? deliveryInfo.phone : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "phone")}
                                    placeholder="-"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "-"}
                                />
                                <p className="PcPost-Text">郵便番号 半角数字</p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.postCode ? deliveryInfo.postCode : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "postCode")}
                                    placeholder="-"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "-"}
                                />



                                <p className="PcPost-Text">都道府県</p>

                                <select
                                    id="prefecture"
                                    className="custom-select"
                                    value={deliveryInfo && deliveryInfo.prefecture}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "prefecture")}
                                    defaultValue="選択してください"
                                >
                                    <option disabled>選択してください</option>
                                    <option value="北海道">北海道</option>
                                    <option value="青森県">青森県</option>
                                    <option value="岩手県">岩手県</option>
                                    <option value="宮城県">宮城県</option>
                                    <option value="秋田県">秋田県</option>
                                    <option value="山形県">山形県</option>
                                    <option value="福島県">福島県</option>
                                    <option value="茨城県">茨城県</option>
                                    <option value="栃木県">栃木県</option>
                                    <option value="群馬県">群馬県</option>
                                    <option value="埼玉県">埼玉県</option>
                                    <option value="千葉県">千葉県</option>
                                    <option value="東京都">東京都</option>
                                    <option value="神奈川県">神奈川県</option>
                                    <option value="新潟県">新潟県</option>
                                    <option value="富山県">富山県</option>
                                    <option value="石川県">石川県</option>
                                    <option value="福井県">福井県</option>
                                    <option value="山梨県">山梨県</option>
                                    <option value="長野県">長野県</option>
                                    <option value="岐阜県">岐阜県</option>
                                    <option value="静岡県">静岡県</option>
                                    <option value="愛知県">愛知県</option>
                                    <option value="三重県">三重県</option>
                                    <option value="滋賀県">滋賀県</option>
                                    <option value="京都府">京都府</option>
                                    <option value="大阪府">大阪府</option>
                                    <option value="兵庫県">兵庫県</option>
                                    <option value="奈良県">奈良県</option>
                                    <option value="和歌山県">和歌山県</option>
                                    <option value="鳥取県">鳥取県</option>
                                    <option value="島根県">島根県</option>
                                    <option value="岡山県">岡山県</option>
                                    <option value="広島県">広島県</option>
                                    <option value="山口県">山口県</option>
                                    <option value="徳島県">徳島県</option>
                                    <option value="香川県">香川県</option>
                                    <option value="愛媛県">愛媛県</option>
                                    <option value="高知県">高知県</option>
                                    <option value="福岡県">福岡県</option>
                                    <option value="佐賀県">佐賀県</option>
                                    <option value="長崎県">長崎県</option>
                                    <option value="熊本県">熊本県</option>
                                    <option value="大分県">大分県</option>
                                    <option value="宮崎県">宮崎県</option>
                                    <option value="鹿児島県">鹿児島県</option>
                                    <option value="沖縄県">沖縄県</option>
                                </select>

                                <p className="PcPost-Text">市区町村</p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.city ? deliveryInfo.city : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "city")}
                                    placeholder="例：新宿区"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "例：新宿区"}
                                />

                                <p className="PcPost-Text">丁目番地</p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.street ? deliveryInfo.street : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "street")}
                                    placeholder="例：愛住町　1-35"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "例：愛住町　1-35"}
                                />

                                <p className="PcPost-Text">建物名・部屋番号</p>
                                <input
                                    className="custom-input"
                                    value={deliveryInfo && deliveryInfo.buildingName ? deliveryInfo.buildingName : null}
                                    readOnly={!isEditable}
                                    onChange={(e) => handleInputChange(e, "buildingName")}
                                    placeholder="例：○○ビル○○○号"
                                    onFocus={(e) => e.target.placeholder = ''}
                                    onBlur={(e) => e.target.placeholder = "例：○○ビル○○○号"}
                                />

                                {/*<p style={{color:"#A4A4A4",fontSize:"14px"}}>※住所はすべて含めて48文字以上入力することができません。</p>*/}
                                <div className="PcAQInside3">
                                    <button className="PcAQ-btn-L" onClick={() => setIsEditable(true)} >編集</button>
                                    <button className="PcAQ-btn-R" onClick={() => {
                                        setIsEditable(false);
                                        updateDeliveryInfo();
                                    }} >保存</button>
                                </div>
                            </div>

                        </div>
                    </div>

                </div>
            </div>

            <div style={{
                background: "white",
                height: "300px",
                width: "100%"
            }}>

            </div>
        </>
    )
}


export default PcPost;