import React, { useState } from "react";
import axios from "axios";
import { useLocation } from "react-router-dom";
import styled from 'styled-components'
import iconSuccess from './imgs/icon-success.png'
import iconClose from './imgs/icon-close.png'
import './PcCreditCard.css'
import filterMoney from "../../../../utils/filterMoney";
import APIHelper from "../../../../utils/APIHelper";
const UNPAYToken = 'MmHnapybWnqhWK1xImVM.eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..9CVoe7NMH7rNYdy7ci7vMVSc4MZXLS7g_pEI5-pGEOY'
function PcCreditCard() {
    const location = useLocation();
    const settleData = location.state && location.state.settleData;
    const [cardHolder, setCardHolder] = useState("");
    const [cardNumber, setCardNumber] = useState("");
    const [expMonth, setExpMonth] = useState("");
    const [expYear, setExpYear] = useState("");
    const [cvv, setCvv] = useState("");
    const [isInputValid, setInputValid] = useState(true);
    const [isCardNumberValid, setCardNumberValid] = useState(true);
    const [showSuccessModal, setShowSuccessModal] = useState(false)
    const [response, setResponse] = useState(null);
    console.log(settleData);


    // const makePayment = async () => {
    //     try {
    //          const token = "YOUR_TOKEN_HERE";  // 替换为你的token
    //
    //         const result = await axios.post('https://reborn-ec.net/payment/card', {
    //             transactionTokenId: '11ee57d0-bc5c-1b90-8f22-e35b4bb56e6d',
    //             orderId: settleData.orderId
    //         }, {
    //             headers: {
    //                 'Authorization': `Bearer ${token}`  // 设置token到请求头
    //             }
    //         });
    //
    //         setResponse(result.data);
    //     } catch (error) {
    //         console.error("Error making payment:", error);
    //         setResponse("Error making payment. Please check the console.");
    //     }
    // };





    const handleCardNumberChange = (e) => {



        const value = e.target.value;
        setCardNumber(value);

        const regex = /^\d{16}$/; // 验证16位数字的正则表达式
        if (regex.test(value)) {
            setCardNumberValid(true);
        } else {
            setCardNumberValid(false);
        }
    }

    const GetCreditCardInfo = () => {
        setShowSuccessModal(true);
        APIHelper.payment({
            "addressInfo": {
                "city": "TOKYO",
                "country": "JAPAN",
                "email": "dummy-email@univapay.com",
                "line1": "Minato-ku, Roppongi, 3-16-35",
                "line2": "East Roppongi building 2F",
                "postalCode": "1060032",
                "state": "TOKYO"
            },
            "cardInfo": {
                "cardHolder": cardHolder,
                "cardNumber": cardNumber,
                "cvv": cvv,
                "expMonth": expMonth,
                "expYear": expYear,
            },
            "chargeAmount": settleData.totalPriceToPay,
            "orderId": "string",
            "requestedCurrency": "JPY"
        }, settleData.orderId).then((e) => {
            console.log(e);
            setShowSuccessModal(true)
        }).catch((err) => {
            console.log(err);
        })
    }

    const handleExpInputChange = (value) => {
        // 使用正则表达式验证输入格式
        const regex = /^(\d{2})\/(\d{2})$/;
        const match = value.match(regex);
        if (match) {
            const [, yearSuffix, month] = match;
            setExpYear("20" + yearSuffix); // 将99转换为2099
            setExpMonth(month);           // 设置月份
            setInputValid(true);          // 设置输入为有效
        } else {
            setInputValid(false);         // 设置输入为无效
        }
    }

    return (
        <>

            <div className="PcCreditCard-container-out">
                <div className="PcCreditCard-container">
                    <div className="PcCreditCard-container-left">
                        <div className="PcCreditCard-container-left-left">
                            <div className="PcCreditCard-container-left-left-Text">
                                PAYMENT
                            </div>

                            <div className="PcCreditCard-container-left-left-button">

                                <div className="PcCreditCard-container-left-left-button-text">
                                    <span style={{
                                        marginLeft: "3px",
                                    }}>クレジットカード</span>
                                    <svg width="105" height="16" viewBox="0 0 105 16" fill="none" xmlns="http://www.w3.org/2000/svg">
                                        <rect x="0.5" y="0.5" width="22" height="15" rx="2" fill="white" stroke="#D9D9D9" />
                                        <path d="M7.02517 10.8392H5.63182L4.58697 6.79524C4.53738 6.60922 4.43208 6.44477 4.27718 6.36726C3.89063 6.17248 3.46468 6.01746 3 5.93928V5.78359H5.24458C5.55437 5.78359 5.78671 6.01746 5.82543 6.28908L6.36756 9.20609L7.76023 5.78359H9.11486L7.02517 10.8392ZM9.88932 10.8392H8.57341L9.65698 5.78359H10.9729L9.88932 10.8392ZM12.6754 7.18413C12.7141 6.91184 12.9464 6.75615 13.2175 6.75615C13.6434 6.71706 14.1074 6.79524 14.4947 6.98935L14.727 5.90087C14.3398 5.74517 13.9138 5.66699 13.5273 5.66699C12.2501 5.66699 11.3207 6.36726 11.3207 7.33915C11.3207 8.07851 11.979 8.46673 12.4437 8.7006C12.9464 8.9338 13.14 9.08949 13.1013 9.32269C13.1013 9.67249 12.7141 9.82818 12.3275 9.82818C11.8629 9.82818 11.3982 9.71158 10.9729 9.5168L10.7406 10.606C11.2052 10.8001 11.708 10.8783 12.1726 10.8783C13.6047 10.9167 14.4947 10.2171 14.4947 9.167C14.4947 7.84464 12.6754 7.76713 12.6754 7.18413ZM19.1 10.8392L18.0552 5.78359H16.9329C16.7005 5.78359 16.4682 5.93928 16.3907 6.17248L14.4559 10.8392H15.8106L16.081 10.1005H17.7454L17.9003 10.8392H19.1ZM17.1265 7.14505L17.513 9.05041H16.4294L17.1265 7.14505Z" fill="#172B85" />
                                        <rect x="27.5" y="0.5" width="22" height="15" rx="2" fill="white" stroke="#D9D9D9" />
                                        <path d="M38.7144 11.6971C37.8368 12.4334 36.6984 12.8779 35.4545 12.8779C32.6788 12.8779 30.4287 10.6649 30.4287 7.93505C30.4287 5.20518 32.6788 2.99219 35.4545 2.99219C36.6984 2.99219 37.8368 3.43668 38.7144 4.17299C39.592 3.43669 40.7304 2.9922 41.9744 2.9922C44.75 2.9922 47.0001 5.2052 47.0001 7.93507C47.0001 10.6649 44.75 12.8779 41.9744 12.8779C40.7304 12.8779 39.592 12.4334 38.7144 11.6971Z" fill="#ED0006" />
                                        <path d="M38.7144 11.6971C39.795 10.7905 40.4802 9.44149 40.4802 7.93505C40.4802 6.42862 39.795 5.07958 38.7144 4.17297C39.592 3.43667 40.7303 2.99219 41.9743 2.99219C44.7499 2.99219 47 5.20518 47 7.93505C47 10.6649 44.7499 12.8779 41.9743 12.8779C40.7303 12.8779 39.592 12.4334 38.7144 11.6971Z" fill="#F9A000" />
                                        <path d="M38.7145 11.697C39.7951 10.7904 40.4803 9.44133 40.4803 7.93491C40.4803 6.42849 39.7951 5.07946 38.7145 4.17285C37.6339 5.07946 36.9487 6.42849 36.9487 7.93491C36.9487 9.44133 37.6339 10.7904 38.7145 11.697Z" fill="#FF5E00" />
                                        <rect x="54.5" y="0.5" width="22.3333" height="15" rx="2" fill="white" stroke="#D9D9D9" />
                                        <path d="M59.3804 4.76261C59.4001 3.96657 60.1097 3.27946 60.9005 3.26782C61.6901 3.26539 62.4799 3.26747 63.2695 3.26677C63.2673 5.93016 63.2738 8.59384 63.2663 11.2571C63.2358 12.0435 62.5339 12.7175 61.7518 12.7314C60.9607 12.7343 60.1695 12.7318 59.3783 12.7326V9.40793C60.1468 9.58948 60.9526 9.66676 61.7366 9.54634C62.2052 9.47094 62.718 9.24081 62.8766 8.75464C62.9934 8.33877 62.9276 7.9011 62.945 7.47427V6.48303H61.5883C61.5822 7.13861 61.6008 7.79534 61.5785 8.45013C61.5419 8.8526 61.1433 9.10832 60.7637 9.09466C60.293 9.09965 59.3601 8.75349 59.3601 8.75349C59.3578 7.5252 59.3738 5.98695 59.3804 4.76273V4.76261Z" fill="url(#paint0_linear_745_8386)" />
                                        <path d="M68.8786 8.19119C69.221 8.19863 69.5654 8.17607 69.9065 8.20291C70.2519 8.26735 70.3352 8.79027 70.0283 8.96153C69.8189 9.07435 69.5702 9.00355 69.3432 9.02345H68.8786V8.19119ZM70.1045 7.24918C70.1806 7.51773 69.9217 7.75885 69.6631 7.72187H68.8786C68.8841 7.46861 68.8678 7.19373 68.8865 6.95381C69.2008 6.96266 69.518 6.93576 69.8304 6.96788C69.9647 7.00161 70.077 7.11197 70.1045 7.24918ZM71.9925 3.26662C72.0071 3.77949 71.9946 4.31946 71.9988 4.84273C71.9978 6.97016 72.0008 9.09753 71.9971 11.2251C71.9833 12.0224 71.2767 12.7152 70.4849 12.7311C69.6924 12.7343 68.8997 12.7316 68.1071 12.7325V9.51625C68.9707 9.51179 69.8348 9.5253 70.6981 9.5095C71.0986 9.48436 71.5372 9.22012 71.5559 8.7794C71.603 8.33678 71.1857 8.03063 70.7895 7.98227C70.6372 7.97832 70.6417 7.93788 70.7895 7.92023C71.1674 7.83856 71.4641 7.44745 71.353 7.05576C71.2582 6.64379 70.8029 6.48434 70.4241 6.48514C69.6519 6.47989 68.8795 6.4844 68.1072 6.48288C68.1122 5.88245 68.0968 5.28137 68.1155 4.68138C68.1767 3.89847 68.9011 3.25282 69.6818 3.26683C70.4521 3.26671 71.2223 3.26671 71.9925 3.26668V3.26662Z" fill="url(#paint1_linear_745_8386)" />
                                        <path d="M63.7754 6.88299C63.704 6.89814 63.761 6.63973 63.7428 6.54171C63.7476 5.92191 63.7326 5.30143 63.7511 4.68207C63.8121 3.89584 64.542 3.24858 65.3258 3.26698H67.6341C67.632 5.93037 67.6384 8.59406 67.6309 11.2573C67.6004 12.0437 66.8985 12.7177 66.1163 12.7316C65.3252 12.7346 64.5339 12.7321 63.7427 12.7328V9.09022C64.2831 9.53358 65.0175 9.60259 65.6907 9.60379C66.1982 9.60362 66.7027 9.52537 67.1956 9.40833V8.74099C66.6402 9.01781 65.9873 9.19364 65.3716 9.0346C64.9421 8.9277 64.6303 8.51262 64.6372 8.06938C64.5875 7.60844 64.8577 7.1218 65.3106 6.98477C65.873 6.80871 66.486 6.94336 67.0131 7.17226C67.1261 7.2314 67.2407 7.30475 67.1955 7.11597V6.59144C66.3139 6.38167 65.3756 6.30448 64.4898 6.53268C64.2334 6.60501 63.9837 6.7147 63.7754 6.88305V6.88299Z" fill="url(#paint2_linear_745_8386)" />
                                        <rect x="81.8335" y="0.5" width="22" height="15" rx="2" fill="#1F72CD" stroke="#D9D9D9" />
                                        <path d="M85.4778 5.62012L83.3335 10.5758H85.9005L86.2188 9.78562H86.9462L87.2644 10.5758H90.09V9.9727L90.3418 10.5758H91.8034L92.0552 9.95995V10.5758H97.9316L98.6462 9.80614L99.3152 10.5758L102.333 10.5821L100.182 8.11177L102.333 5.62012H99.362L98.6665 6.37551L98.0185 5.62012H91.6257L91.0767 6.89921L90.5149 5.62012H87.9532V6.20265L87.6682 5.62012H85.4778ZM94.2531 6.32382H97.6276L98.6597 7.48812L99.7251 6.32382H100.757L99.189 8.11107L100.757 9.87772H99.6783L98.6462 8.6999L97.5754 9.87772H94.2531V6.32382ZM95.0864 7.70924V7.06009V7.05947H97.192L98.1108 8.09762L97.1513 9.14145H95.0864V8.43277H96.9274V7.70924H95.0864ZM85.9745 6.32382H87.2258L88.6481 9.68427V6.32382H90.0188L91.1174 8.73324L92.1299 6.32382H93.4938V9.87982H92.6639L92.6571 7.09336L91.4472 9.87982H90.7048L89.4881 7.09336V9.87982H87.7808L87.4572 9.08261H85.7085L85.3855 9.87912H84.4707L85.9745 6.32382ZM86.0071 8.34564L86.5832 6.92548L87.1586 8.34564H86.0071Z" fill="white" />
                                        <defs>
                                            <linearGradient id="paint0_linear_745_8386" x1="59.2955" y1="8.09033" x2="63.2057" y2="8.09033" gradientUnits="userSpaceOnUse">
                                                <stop />
                                                <stop offset="1" />
                                            </linearGradient>
                                            <linearGradient id="paint1_linear_745_8386" x1="68.0805" y1="7.79716" x2="71.9742" y2="7.79716" gradientUnits="userSpaceOnUse">
                                                <stop />
                                                <stop offset="1" />
                                            </linearGradient>
                                            <linearGradient id="paint2_linear_745_8386" x1="63.737" y1="7.88922" x2="67.6334" y2="7.88922" gradientUnits="userSpaceOnUse">
                                                <stop />
                                                <stop offset="1" />
                                            </linearGradient>
                                        </defs>
                                    </svg>

                                </div>

                                <p>カード名義</p>

                                <input className="inputText" type="text"
                                    value={cardHolder}
                                    onChange={(e) => setCardHolder(e.target.value)}
                                    style={{ fontSize: '30px' }}
                                />

                                <p>カード番号</p>

                                <input
                                    className={`inputText ${!isCardNumberValid ? 'invalid-input' : ''}`}
                                    type="text"
                                    maxLength="16"
                                    value={cardNumber}
                                    onChange={handleCardNumberChange}
                                    style={{ fontSize: '30px' }}
                                />


                                <div className="PcCreditCard-bottom-input">
                                    <div className="PcCreditCard-bottom-input-inline">
                                        <span>有効期限</span>
                                        <input className={`inputBottomText ${!isInputValid ? 'invalid-input' : ''}`} // 根据isInputValid的值设置类名
                                            type="text"
                                            placeholder="YY/MM"
                                            pattern="\d{2}/\d{2}"
                                            required
                                            onChange={(e) => handleExpInputChange(e.target.value)}
                                            style={{ fontSize: '30px' }}
                                        />
                                    </div>
                                    <div className="PcCreditCard-bottom-input-inline">
                                        <span>セキュリティーコード</span>
                                        <input className="inputBottomText" type="password"
                                            value={cvv}
                                            onChange={(e) => setCvv(e.target.value)}
                                            style={{ fontSize: '30px' }}
                                        />
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                    <div className="PcCreditCard-container-right">
                        <div style={{
                            width: "100%",
                            height: "30px",
                        }}></div>

                        <div className="PcCreditCard-container-right-box">
                            <div className="PcCreditCard-container-right-box-inline">
                                <div style={{ fontSize: "20px" }}>
                                    注文内容
                                </div>
                                <div style={{
                                    fontSize: "16px",
                                    borderBottom: "1px solid #A4A4A4",
                                    paddingBottom: "13px",
                                }}>
                                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                                        <div>商品小計：</div>
                                        <div>{filterMoney(settleData && settleData.productPrice)}</div>
                                    </div>
                                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                                        <div>配送料：</div>
                                        <div>
                                            {filterMoney(settleData && settleData.deliveryCharge)}
                                        </div>
                                    </div>
                                </div>
                                <div style={{
                                    fontSize: "20px",
                                    color: "#FE3D3D",
                                    borderBottom: "1px solid #A4A4A4",
                                    paddingBottom: "13px",
                                }}>
                                    <div style={{ display: "flex", justifyContent: "space-between" }}>
                                        <div>お支払額</div>
                                        <div>
                                            {filterMoney(settleData && settleData.totalPriceToPay)}
                                        </div>
                                    </div>
                                </div>
                                <button className="PcCreditCard-btm" style={{
                                    width: "320px",
                                    height: "56px",
                                    background: "#F57948",
                                }}
                                    onClick={GetCreditCardInfo}>
                                    支払う
                                </button>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
            {showSuccessModal && <SuccessModal>
                <div className="mask" />
                <div className="content-box">
                    <img src={iconClose} className="close" onClick={() => setShowSuccessModal(false)} />
                    <img src={iconSuccess} className="img" />
                    <span className="font">購入完了</span>
                </div>
            </SuccessModal>}
        </>
    )
}
export default PcCreditCard;

const SuccessModal = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    .mask{
        background:rgba(0, 0, 0, 0.6);
        width: 100%;
        height: 100%;
    }
    .content-box{
        width: 480px;
        height: 480px;
        position:absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
        .img{
            width: 36px;
            height: 36px;
            margin-right: 16px;
        }
        .font{
            font-size: 20px;
            font-weight: 500;
            line-height: 30px;
        }
        .close{
            width: 48px;
            height: 48px;
            top: 16px;
            right: 16px;
            position: absolute;
            cursor: pointer;
        }
    }
`