import React, { useState } from "react";
import axios from "axios";
import './PcNewAccount.css'
import styled from 'styled-components'
import iconSuccess from '../PcPayPage/PayPage/imgs/icon-success.png'
import iconClose from '../PcPayPage/PayPage/imgs/icon-close.png'
import APIHelper from "../../../utils/APIHelper";

function PcNewAccount() {


    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [email, setEmail] = useState('');
    const [tel, setTel] = useState('');
    const [showSuccessModal, setShowSuccessModal] = useState(false)
    const [modalMessage, setModalMessage] = useState(true)

    const register = () => {
        APIHelper.accountCreate({
            email: email,
            password: password,
            phone: tel,
            roleId: 'CUSTOMER',
            status: 0,
            username: username,
        }).then((res) => {
            if (res.data.code === '200') {
                setModalMessage(true)
                setShowSuccessModal(true)
            } else {
                return Promise.reject()
            }
        }
        ).catch(() => {
            setModalMessage(false)
            setShowSuccessModal(true)
        });

    }

    return (
        <>
            <div className="PcNewAccount-container">
                <div className="PcNew-container">
                    <div className="PcNew-items-container">
                        <div className="Login-text">
                            新規登録
                        </div>
                        <input
                            type="text"
                            onChange={(e) => setUsername(e.target.value)}
                            placeholder="Username"
                        />
                        <input
                            type="password"
                            onChange={(e) => setPassword(e.target.value)}
                            placeholder="Password"
                        />
                        <input
                            type="email"
                            onChange={(e) => setEmail(e.target.value)}
                            placeholder="hello@japanmail.com"
                        />
                        <input
                            type="tel"
                            onChange={(e) => setTel(e.target.value)}
                            placeholder="08088888888"
                        />
                        <button onClick={register}>登録</button>

                    </div>
                </div>
            </div>
            {showSuccessModal && <SuccessModal>
                <div className="mask" />
                <div className="content-box">
                    <img src={iconClose} className="close" onClick={() => setShowSuccessModal(false)} />
                    {modalMessage ? <>
                        <img src={iconSuccess} className="img" />
                        <span className="font">ユーザ登録完了しました</span>
                    </> : <span className="font">ユーザ登録が失敗しました、再度と試し下さい</span>}
                </div>
            </SuccessModal>}
        </>
    )
}

export default PcNewAccount;

const SuccessModal = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    .mask{
        background:rgba(0, 0, 0, 0.6);
        width: 100%;
        height: 100%;
    }
    .content-box{
        width: 480px;
        height: 480px;
        position:absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        background-color: #ffffff;
        display: flex;
        align-items: center;
        justify-content: center;
        .img{
            width: 36px;
            height: 36px;
            margin-right: 16px;
        }
        .font{
            font-size: 20px;
            font-weight: 500;
            line-height: 30px;
        }
        .close{
            width: 48px;
            height: 48px;
            top: 16px;
            right: 16px;
            position: absolute;
            cursor: pointer;
        }
    }
`