import React from "react";
import './PcAQ.css'
import axios from "axios";
import APIHelper from "../../../utils/APIHelper";
let api = '/api/qa/create'



function PcAQ() {
    const SendMessage = () => {
        APIHelper.qaCreat({
            id: "string",
            email: "string",
            question: "string",
            userName: "string"
        }).then(
            (e) => {
                console.log(e);
                alert("送信完了");
            }
        )
    }



    return (
        <>
            <div className="AQNav">
                <div className="AQText">HOME / お問い合わせ</div>

            </div>
            <div className="TEXTAQ">
                お問い合わせ
            </div>

            <div className="PcAQ-container">
                <div className="PcAQInside">
                    <div className="PcAQInside2">

                        <p>お名前</p>
                        <input className="custom-input"
                            placeholder="お名前"
                            onFocus={(e) => e.target.placeholder = ''}
                            onBlur={(e) => e.target.placeholder = "お名前"}
                        />

                        <p><span style={{
                            color: "red"
                        }}>＊</span>メールアドレス</p>
                        <input className="custom-input"
                            placeholder="-"
                            onFocus={(e) => e.target.placeholder = ''}
                            onBlur={(e) => e.target.placeholder = "-"}
                        />
                        <p><span style={{
                            color: "red"
                        }}>＊</span>お問い合わせ内容</p>
                        <textarea className="custom-input2"
                            placeholder="１０００文字まで"
                            style={{
                                paddingTop: "10px",
                            }}
                            onFocus={(e) => e.target.placeholder = ''}
                            onBlur={(e) => e.target.placeholder = "１０００文字まで"}
                        />
                        <div style={{
                            display: "flex",
                            justifyContent: "center",
                            alignItems: "center",
                            width: "544px",
                            height: "156px",
                        }}>
                            <button className="PcAQ-btn" onClick={SendMessage}>送信</button>
                        </div>
                    </div>

                </div>
            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcAQ;


