import React, { useEffect, useState } from "react";
import './PcPCReceiptPaper.css';
import axios from "axios";
import APIHelper from "../../../utils/APIHelper";

function PcPCReceiptPaper() {
    const [data, setData] = useState(null);
    const myToken = sessionStorage.getItem('reborn-ec-token');
    const url = window.location.href;
    const parts = url.split('/');
    const id = parts[parts.length - 1];

    useEffect(() => {
        const fetchData = async () => {
            if (id) {
                try {
                    const response = await APIHelper.getPaymentDetail(id);
                    setData(response.data.data);
                } catch (error) {
                    console.error("Error fetching data:", error);
                }
            }
        };

        fetchData();
    }, [id, myToken]);

    if (!data) return <div>Loading...</div>;

    return (
        <div>


            <p>店舗名:{data.shopName}</p>
            <p>店舗の登録番号:{data.invoice}</p>
            <p>取引年月日:{data.paymentTime.match(/^(\d{4}-\d{2}-\d{2})/)[1]}</p>
            <table>
                <thead>
                    <tr style={{
                        "borderBottom": "1px solid #626264",
                    }}
                    >
                        <th>購入品目</th>
                        <th>数量</th>
                        <th>税抜金額</th>
                    </tr>
                </thead>
                <tbody>
                    {data.payments.map(payment => (
                        <tr key={payment.productId}>
                            <td>{payment.productName}{payment.taxRate === 8 ? "※ " : ""}</td>
                            <td>{payment.quantity}</td>
                            <td>{payment.price}</td>
                        </tr>
                    ))}
                    <tr>
                        <td colSpan="3" style={{
                            "borderBottom": "1px solid #626264",
                        }}>
                            小計:
                        </td>
                    </tr>
                    <tr>
                        <td>10% 税：</td>
                        <td>{data.tax10TotalQuantity}</td>
                        <td>{data.tax10TotalPrice}</td>
                    </tr>
                    <tr>
                        <td>8% 税：</td>
                        <td>{data.tax8TotalQuantity}</td>
                        <td>{data.tax8TotalPrice}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>合計</td>
                        <td>{data.totalPrice}</td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>※は軽減税率(8%)対象</td>
                        <td></td>
                    </tr>
                </tbody>
            </table>


            <div style={{
                "width": "100%",
                "height": "200px"
            }}></div>
        </div>
    );
}

export default PcPCReceiptPaper;


