import React, { useEffect, useState } from "react";
import './PcCartNext.css'
import axios from "axios";
import { useNavigate } from 'react-router-dom';
import filterMoney from "../../../utils/filterMoney";
import APIHelper from "../../../utils/APIHelper";


function PcCartNext() {
    const navigate = useNavigate();
    const [settle, setSettle] = useState({
        productPrice: 0,
        deliveryCharge: 0,
        totalAmount: 0,
        totalPriceToPay: 0,
    });
    const [product, setProduct] = useState([]);
    const [deliveryInfo, setDeliveryInfo] = useState({
        name: "",
        phone: "",
        postCode: "",
        prefecture: "",
        city: "",
        street: "",
        buildingName: "",
    });
    useEffect(() => {
        const fetchDeliveryInfo = () => {
            return APIHelper.getUserDelivery();
        }
        const fetchProduct = () => {
            return APIHelper.getSc();
        }

        const fetchProductScSettle = () => {
            return APIHelper.getSettle({ deliveryInfo }).then(res => {
                setSettle(res.data.data);
                console.log(settle);
            })
        }


        Promise.all([fetchDeliveryInfo(), fetchProduct(), fetchProductScSettle()])
            .then(([deliveryInfoResponse, productResponse]) => {
                setDeliveryInfo(deliveryInfoResponse.data.data);
                setProduct(productResponse.data.data);
                console.log(productResponse.data.data);
                //console.log(product);
            })
            .catch(error => {
                console.error("Error fetching data:", error);
            });

    }, []);


    return (
        <>
            <div className="PcCartNext-container-outer">

                <div className="PcCartNext-container">

                    <div className="PcCartNext-container-left">
                        <div className="PcCartNext-container-PAYMENT-Nav">
                            PAYMENT
                        </div>
                        <div className="PcCartNext-PAYMENT-Address-confirm">
                            <div className="PcCartNext-PAYMENT-Address-confirm-left">
                                <div className="PcCartNext-PAYMENT-Address-confirm-left-left">
                                    I.　お届け場所
                                </div>

                                <div className="PcCartNext-PAYMENT-Address-confirm-left-middle">
                                    {/*地址详情*/}
                                    <div>
                                        名前:{deliveryInfo && deliveryInfo.name}
                                    </div>
                                    <div>
                                        <span> {deliveryInfo && deliveryInfo.prefecture}</span>
                                        <span>{deliveryInfo && deliveryInfo.city}</span>
                                        <span>  {deliveryInfo && deliveryInfo.street}</span>
                                        <span>
                                            {deliveryInfo && deliveryInfo.buildingName}
                                        </span>
                                    </div>
                                </div>
                                <div className="PcCartNext-PAYMENT-Address-confirm-left-right">
                                    <button style={{ color: "#3F8D39" }} onClick={() => {
                                        navigate('/delivery-address');
                                    }}>
                                        変更
                                    </button>
                                </div>
                            </div>
                            <div className="PcCartNext-PAYMENT-Address-confirm-right">
                                II.　商品確認
                                {product.map(pd => (
                                    <div className="productMap" key={pd.productId}>
                                        <div className="productMapLeft">
                                            <img src={pd.image} alt="Product" width="50" />
                                        </div>
                                        <div className="productMapRight"
                                            style={{
                                                "marginLeft": "20px"
                                            }}
                                        >
                                            <p>{pd.productName}</p>
                                            <p>数量:{pd.quantity}</p>
                                            <p>価格:{filterMoney(pd.singleTotalPrice)}</p>
                                        </div>

                                    </div>
                                ))}
                            </div>
                        </div>


                    </div>

                    <div className="PcCartNext-container-right">
                        <div className="PcCartNext-container-accounts">
                            <div className="PcCartNext-container-accounts-div" style={{ fontSize: "20px" }}>
                                <div>  商品小計</div>
                                <div>{filterMoney(settle && settle.productPrice ? settle.productPrice : 'Loading...')}</div>
                            </div>
                            <div className="PcCartNext-container-accounts-div" style={{ fontSize: "16px", borderBottom: "1px solid #A4A4A4", paddingBottom: "25px", }}>
                                <div>配送料：</div>
                                <div> {filterMoney(settle && settle.deliveryCharge >= 0 ? settle.deliveryCharge : 'Loading...')}</div>
                            </div>
                            <div className="PcCartNext-container-accounts-div" style={{ fontSize: "16px" }}>
                                <div> 合計：</div>
                                <div>{filterMoney(settle && settle.totalAmount ? settle.totalAmount : 'Loading...')}</div>
                            </div>
                            <div className="PcCartNext-container-accounts-div" style={{ fontSize: "16px", borderBottom: "1px solid #A4A4A4", paddingBottom: "25px", }}>
                                <div>お支払額</div>
                                <div>{filterMoney(settle && settle.totalPriceToPay ? settle.totalPriceToPay : 'Loading...')}</div>
                            </div>
                            <button className="buy-btn" onClick={
                                () => {
                                    navigate('/PcPayPage', { state: { settleData: settle } });
                                }
                            }>支払い</button>
                            <button className="continue-btn" onClick={
                                () => {
                                    window.location.href = '/';
                                }
                            }>戻る</button>
                        </div>
                    </div>
                </div>

            </div>
        </>
    )
}

export default PcCartNext;