import React, { useEffect, useState } from "react";
import './PcDescription.css'
import axios from "axios";
import APIHelper from "../../../../utils/APIHelper";


function PcDescription() {


    const [data, setData] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {

        APIHelper.getLaw()
            .then((response) => {
                setData(response.data.data);
                setLoading(false);
            })
            .catch((err) => {
                setError(err);
                setLoading(false);
            });
    }, []);

    if (loading) return <p>Loading...</p>;
    if (error) return <p>Error: {error.message}</p>;
    return (
        <>
            <div className="PcDescriptionNav">
                <div className="PcDescriptionText">HOME / 特定商取引法に基づく表記</div>

            </div>

            <div className="TEXTAQ">
                特定商取引法に基づく表記
            </div>

            <div className="PcDescription-container">
                <div className="PcDescriptionInside">
                    <div className="PcDescriptionInside2">
                        {data}
                    </div>
                </div>
            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcDescription;