import axios from "axios";

const baseHost = 'https://www.ict-reborn.link/api'

const get = (api, ignoreToken) => {
    const myToken = sessionStorage.getItem('reborn-ec-token');
    const headers = ignoreToken ? {} : {
        headers: {
            'Authorization': `Bearer ${myToken}`
        }
    }
    return axios.get(baseHost + api, {
        ...headers
    })
}

const post = (api, data, ignoreToken) => {
    const myToken = sessionStorage.getItem('reborn-ec-token');
    const headers = ignoreToken ? {} : {
        headers: {
            'Authorization': `Bearer ${myToken}`
        }
    }
    return axios.post(baseHost + api, {
        ...data,
    }, { ...headers })
}

export default {
    addCart(productId) {
        return get(`/sc/add/${productId}/1`)
    },
    deleteCart(productId) {
        return get(`/sc/delete/${productId}/1`)
    },
    deleteAllCart(productId, quantity) {
        return get(`/sc/delete/${productId}/${quantity}`)
    },
    getProductDetail(productId) {
        return get(`/product/detail/${productId}`)
    },
    getProductDetailImg(productId) {
        return get(`/products/image/${productId}`)
    },
    addShop(productId, quantity) {
        return get(`/sc/add/${productId}/${quantity}`)
    },
    getLogo() {
        return get(`/shop/logo`, true)
    },
    getUserDetail() {
        return get(`/user/detail`)
    },
    getProductsCategory(SelectedID) {
        return get(`/products/category/${SelectedID}`, true)
    },
    getCategory(SelectedID) {
        return get(`/fetch/category/${SelectedID}`)
    },
    getUserAccess(data) {
        return post(`/user/access`, data)
    },
    getAdvertisementList() {
        return get(`/advertisement/list`, true)
    },
    getBestsellers() {
        return get(`/products/bestsellers`, true)
    },
    getFavoriteList() {
        return get(`/favorite/list`)
    },
    handleFavorite(ProductId, isFavorite) {
        return get(isFavorite ? `/favorite/delete/${ProductId}` : `/favorite/add/${ProductId}`)
    },
    getShopCopyright() {
        return get(`/shop/copyright`, true)
    },
    getMoreCategory(categoryId) {
        return get(categoryId !== '0' ? '/products/category/all/' + categoryId : '/products')
    },
    login(data) {
        return post(`/login`, data, true)
    },
    accountCreate(data) {
        return post(`/admin/account/create`, data, true)
    },
    getSc() {
        return get(`/sc`)
    },
    getScSumAll() {
        return get(`/sc/sum/all`)
    },
    getUserDelivery() {
        return get(`/user/delivery`)
    },
    getSettle(data) {
        return post(`/sc/settle`, data)
    },
    payment(data, orderId) {
        return post(`/payment/ccv/${orderId}`, data)
    },
    getProductDetail(id) {
        return get(`/product/detail/${id}`)
    },
    getHistory() {
        return get(`/payment/history`)
    },
    getPaymentDetail(id) {
        return get(`/payment/detail/${id}`)
    },
    deliveryModify(data) {
        return post(`/user/delivery/modify`, data)
    },
    getSearch(searchValue) {
        return get(`/products/search?productName=` + searchValue, true)
    },
    getPrivacy() {
        return get('/shop/privacy', true)
    },
    getLaw() {
        return get('/shop/law', true)
    },
    getProfile() {
        return get('/shop/profile', true)
    },
    qaCreat(data) {
        return post(`/qa/create`, data)
    },
    getWxPay(chargeId) {
        return get(`/univapay/wechat/charge/${chargeId}`)
    },
    wxPay(data) {
        return post(`/payment/wechat`, data)
    },
}