import React, { useState } from "react";
import { Link } from "react-router-dom";
import './PcMyPage.css'
import axios from "axios";
import APIHelper from "../../../utils/APIHelper";


function PcMyPage() {
    const [MyPageName, setMyPageName] = useState('');
    const myToken = sessionStorage.getItem('reborn-ec-token');  //
    if (myToken) {
        APIHelper.getUserDetail().then(response => {
            console.log('Data sent successfully:', response.data.data);
            setMyPageName(response.data.data.username);
            console.log(MyPageName);
        })
            .catch(error => {
                console.error('Error sending data:', error);
            }, []);
    } else {
        console.error('No data found in sessionStorage');
    }

    return (
        <>
            <div className="MyPageNav">
                <div className="MyPageText">HOME / MY PAGE</div>
            </div>
            <div className="TEXTMyPage">
                MY PAGE
            </div>
            <div style={{
                backgroundColor: "white",
                width: "100%",
                height: "50px",
                display: "flex",
                justifyContent: "center",
            }}>
                <div style={{
                    display: "flex",
                    width: "544px",
                    borderTop: "1px solid black",
                    borderBottom: "1px solid black",
                    justifyContent: "center",
                    alignItems: "center",
                }}>
                    こんにちわ　　　<span className={"name"}>{MyPageName}</span>　様
                </div>
            </div>
            <div style={{
                backgroundColor: "white",
                width: "100%",
                height: "78px",
            }}></div>
            <div className="menu-container">
                <div className="menu-container1">
                    <div className="menu-container2">
                        <div className="NavTop"><Link to="/PCFavorites">お気に入り</Link></div>
                        <div className="NavTop"><Link to="/PcShopHistory">購入履歴</Link></div>
                        <div className="NavTop"><Link to="/receipt">領収書</Link></div>

                        <div className="NavTop"><Link to="/PcAccountManagement">アカウント管理</Link></div>
                        <div className="NavTop"><Link to="/delivery-address">お届け先</Link></div>
                    </div>
                </div>

            </div>

            <div style={{ height: '200px', width: '100%', backgroundColor: "#fff" }}></div> {/*占位*/}
        </>
    )
}

export default PcMyPage;
