import React from 'react';
import { HashRouter as Router, Route, Routes } from 'react-router-dom';
import Navbar from "./common/Navbar/Navbar";
import FooterNav from "./common/FooterNav/FooterNav";
import Home from "./pages/PC/PcHome/Home";
import PCCategory from "./pages/PC/PcCategory/PCCategory";
import PciTem from "./pages/PC/PciTem/PciTem";
import PcAQ from "./pages/PC/PcAQ/PcAQ";
import PcMyPage from "./pages/PC/PcMyPage/PcMyPage";
import PCFavorites from './pages/PC/PcMyPage/favorites/PCFavorites'
import PcHotShop from "./pages/PC/PcHotShop/PcHotShop";
import PcCart from "./pages/PC/PcCart/PcCart";
import PcShopHistory from "./pages/PC/PcShopHistory/PcShopHistory";
import { CartProvider } from "./pages/PC/PcCart/CartContext";
import PcLogin from "./pages/PC/PcLoginINOUT/PcLogin";
import PcAccount from "./pages/PC/PcAccount/PcAccount";
import PcNewAccount from "./pages/PC/PcAccount/PcNewAccount";
import PcSearchAnswer from "./pages/PC/PcSearchAnswer/PcSearchAnswer";
import PcPost from "./pages/PC/PcMyPage/PcPost/PcPost";
import PcCartNext from "./pages/PC/PcCartNext/PcCartNext";
import PCReceipt from "./pages/PC/PCReceipt/PCReceipt";
import PCReceiptNext from "./pages/PC/PCReceipt/PCReceiptNext";
import PcCompany from "./pages/PC/PCFooterNav/Company/PcCompany";
import PcDescription from "./pages/PC/PCFooterNav/Description/PcDescription";
import PcPrivacyPolicy from "./pages/PC/PCFooterNav/PrivacyPolicy/PcPrivacyPolicy";
import PcPayPage from "./pages/PC/PcPayPage/PcPayPage";
import PcCreditCard from "./pages/PC/PcPayPage/PayPage/PcCreditCard";
import PcWeChatPay from "./pages/PC/PcPayPage/PayPage/PcWeChatPay";
import PCAccountManagement from "./pages/PC/PcMyPage/PCAccountManagement/PcAccountManagement";

function App() {
    return (
        <>
            <Router>
                <div>
                    <Navbar />
                    <CartProvider>
                        <Routes>
                            <Route path="/" element={<Home />} />
                            <Route path="/BestSeller" element={<PcHotShop />} />
                            <Route path="/PCCategory" element={<PCCategory />} />
                            <Route path="/MyPage" element={<PcMyPage />} />
                            <Route path="/PcAQ" exact element={<PcAQ />} />


                            <Route path="/product/:id" exact element={<PciTem />} />
                            <Route path="/PcCarts" element={<PcCart />}></Route>
                            <Route path="/PcShopHistory" element={<PcShopHistory />}></Route>

                            <Route path="/PcLoginIn" element={<PcLogin />}></Route>

                            <Route path="/PCFavorites" element={<PCFavorites />}></Route>


                            <Route path="/PcAccount" element={<PcAccount />}></Route>
                            <Route path="/PcAccountManagement" element={<PCAccountManagement />}></Route>

                            <Route path="/PcNewAccount" element={<PcNewAccount />}></Route>
                            <Route path="/PcSearchAnswer" element={<PcSearchAnswer />}></Route>
                            <Route path="/delivery-address" element={<PcPost />}></Route>
                            <Route path="/PcCartsNext" element={<PcCartNext />}></Route>
                            <Route path="/receipt" element={<PCReceipt />}></Route>
                            <Route path="/receiptNext/:id" element={<PCReceiptNext />}></Route>


                            <Route path="/PcCompany" element={<PcCompany />}></Route>
                            <Route path="/PcDescription" element={<PcDescription />}></Route>
                            <Route path="/PcPrivacyPolicy" element={<PcPrivacyPolicy />}></Route>

                            <Route path="/PcPayPage" element={<PcPayPage />}></Route>
                            <Route path="/PcCreditCard" element={<PcCreditCard />}></Route>
                            <Route path="/PcWeChatPay" element={<PcWeChatPay />}></Route>



                        </Routes>
                    </CartProvider>
                    <FooterNav />
                </div>
            </Router>
        </>
    );
}

export default App;


